
@import "../../scss/variables";
#mechanics {
  padding: 150px 0 48px;
  background: #ffffff;

  @media (max-width: 991px) {
    padding: 100px 0 48px;
  }

  @media (max-width: 575px) {
    padding: 26px 0 27px;
  }

  // video:not([src]):not([srcset]) {
  //   visibility: hidden;
  // }

  .title-box {
    width: 100%;
    margin-bottom: 85px;

    @media (max-width: 991px) {
      margin-bottom: 45px;
    }

    @media (max-width: 767px) {
      margin-bottom: 24px;
    }
  }

  .row {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 40px;
    width: 100%;

    @media (max-width: 1199px) {
      gap: 20px;
    }

    @media (max-width: 991px) {
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
  }

  .mechanics {
    max-width: 648px;
    width: 100%;

    @media (max-width: 1199px) {
      width: calc(50% - 10px);
    }

    @media (max-width: 991px) {
      max-width: 100%;
      width: 100%;
    }

    @media (max-width: 767px) {
      position: relative;
      overflow: hidden;
      width: 100%;
      padding-top: 0;
      margin-top: 15px;
    }

    &__img-wrap {
      position: relative;
      max-width: 700px;
      width: 100%;
      @media (max-width: 1199px) {
        max-width: calc(50% - 10px);
      }

      @media (max-width: 991px) {
        max-width: 700px;
      }

      @media (max-width: 767px) {
        max-width: 500px;
      }

      @media (max-width: 575px) {
        max-width: 100%;
        padding: 0 5.973%;
      }

      video {
        max-width: 100%;
      }
    }

    &__wrap {
      @media (min-width: 768px) {
        display: flex;
        flex-direction: column;
        row-gap: 30px;
      }
    }

    &__item {
      @media (min-width: 768px) {
        height: auto;
      }
    }

    &__text {
      font-family: "Inter", sans-serif;
      font-weight: 400;
      font-size: 20px;
      line-height: 28px;
      color: $text-black-gray;

      span {
        font-weight: 700;
        color: $primary-color;
      }
    }

    .mechanics-pagination {
      display: flex;
      justify-content: center;
      margin-bottom: 30px;
      padding-top: 4px;
      padding-left: 30px;
      padding-right: 30px;
      width: 100%;
      z-index: 2;

      @media (min-width: 768px) {
        display: none;
      }

      span {
        width: 20px;
        height: 20px;
        margin: 0 6px;
        background-color: #bdbdbd;
        opacity: 1;

        &.swiper-pagination-bullet-active {
          background-color: $primary-color;
        }
      }
    }

    .mechanics-navigation {
      position: absolute;
      top: 0;
      z-index: 2;
      width: 100%;
      padding: 0;
      display: flex;
      justify-content: space-between;
      pointer-events: none;
      @media (min-width: 768px) {
        display: none;
      }

      .mechanics-btn {
        position: relative;
        width: 28px;
        height: 28px;
        background-color: #ffffff;
        background-position: center;
        background-size: 28px 28px;
        background-repeat: no-repeat;
        cursor: pointer;
        pointer-events: all;
        transition: 0.3s all;
      }

      .mechanics-btn_next {
        background-image: url("../images/global/slider-next.svg");
      }
      .mechanics-btn_prev {
        background-image: url("../images/global/slider-prev.svg");
      }

      .swiper-button-disabled {
        filter: grayscale(1);
      }
    }
  }
}
