
@import "../../scss/variables";
#footer.footer-new {
  width: 100%;
  padding: 60px 0;
  background-color: #2f2f2f;
  overflow: hidden;

  @media (max-width: 575px) {
    padding: 33px 0 27px;
  }

  .content-container {
    flex-direction: column;
  }

  .footer__row {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    row-gap: 40px;
    column-gap: 24px;
    // column-gap: 10.75%;
    width: 100%;
    margin-bottom: 90px;

    @media (max-width: 991px) {
      flex-wrap: wrap;
      justify-content: space-between;
      row-gap: 30px;
      margin-bottom: 40px;
    }

    @media (max-width: 575px) {
      margin-bottom: 30px;
    }
  }

  .footer__logo-col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-shrink: 0;
    margin-right: auto;

    @media (max-width: 991px) {
      width: 100%;
      align-items: left;
    }

    .footer__logo-wrap {
      margin-bottom: 46px;

      @media (max-width: 991px) {
        align-self: center;
      }

      @media (max-width: 575px) {
        max-width: 120px;
      }
    }

    .footer__phone-number {
      margin-bottom: 14px;
      font-family: inherit;
      font-weight: 400;
      font-size: 30px;
      line-height: 36px;
      color: #ffffff;
      transition: 0.4s all;

      &:hover {
        color: $primary-color;
      }

      @media (max-width: 575px) {
        margin-bottom: 4px;
        font-weight: 600;
        font-size: 20px;
        line-height: 26px;
      }
    }

    .footer__phone-text {
      font-family: inherit;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #ffffff;
      opacity: 0.55;
    }

    // .footer__social-wrap {
    //   display: flex;
    //   align-items: center;
    //   gap: 15px;
    //   margin-top: 22px;

    //   .footer__social-item {
    //     display: flex;
    //     align-items: center;
    //     justify-content: center;
    //     min-width: 28px;
    //     min-height: 28px;
    //     border-radius: 50%;
    //     transition: 0.3s all;
    //     background-position: center;
    //     background-size: 80%;
    //     svg {
    //       width: 100%;
    //       height: 100%;
    //     }

    //     &:hover {
    //       transform: scale(1.13) translateY(1px);
    //     }
    //   }
    // }
  }

  .footer__contacts-col {
    width: 100%;
    max-width: 335px;
    .footer__contacts-list {
      display: flex;
      flex-direction: column;
      row-gap: 26px;
      width: 100%;
      list-style: none;
      margin: 0;
      padding: 0;

      @media (max-width: 575px) {
        row-gap: 16px;
      }

      li {
        font-weight: inherit;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: rgba(255, 255, 255, 0.7);

        @media (max-width: 575px) {
          font-weight: 600;
          font-size: 14px;
          line-height: 1.4;
        }

        span {
          color: rgba(255, 255, 255, 1);

          @media (max-width: 575px) {
            font-weight: 700;
          }
        }
      }
    }
  }

  .footer__links-col {
    width: 100%;
    max-width: calc(270px + 10.75% - 24px);
    padding-left: calc(10.75% - 24px);

    @media (max-width: 991px) {
      max-width: 270px;
      padding-left: 0;
    }

    .footer__links-list {
      display: flex;
      flex-direction: column;
      row-gap: 18px;
      width: 100%;
      list-style: none;
      margin: -3px 0 0 0;
      padding: 0;

      @media (max-width: 575px) {
        row-gap: 10px;
      }

      li {
        font-family: inherit;
        width: 100%;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        @media (max-width: 575px) {
          font-size: 14px;
          line-height: 1.4;
        }

        a {
          padding: 3px 0;
          text-align: center;
          font-family: inherit;
          width: 100%;
          font-weight: inherit;
          font-size: inherit;
          line-height: inherit;
          color: #ffffff;
          transition: 0.4s all;
          background: linear-gradient(
            to left,
            #ffffff 50%,
            $primary-color 50%
          ) !important;
          -webkit-background-clip: text !important;
          -webkit-text-fill-color: transparent !important;
          background-size: 200% 100% !important;
          background-position: right !important;

          @media (max-width: 575px) {
            text-decoration-line: underline;
          }

          &:hover {
            background: linear-gradient(
              to left,
              #ffffff 50%,
              $primary-color 50%
            ) !important;
            -webkit-background-clip: text !important;
            -webkit-text-fill-color: transparent !important;
            background-size: 200% 100% !important;
            background-position: left !important;
          }
        }
      }
    }

    .footer__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 50px;
      width: 100%;
      max-width: 224px;
      min-height: 56px;
      padding: 10px 15px;
      margin-top: 24px;
      font-family: inherit;
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      text-align: center;
      letter-spacing: 0.01em;
      text-transform: none;

      &::before {
        display: none;
      }

      @media (max-width: 575px) {
        max-width: 185px;
        min-height: 50px;
        border-radius: 3px;
        font-weight: 600;
        font-size: 18px;
        line-height: 30px;
        margin-top: 20px;
      }
    }
  }

  .footer__copyright-col {
    position: relative;
    width: 100%;
    padding-top: 31px;

    @media (max-width: 575px) {
      padding-top: 26px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 110vw;
      height: 1px;
      background: #ffffff;
      opacity: 0.1;

      @media (max-width: 575px) {
        width: 100%;
      }
    }

    .footer__copyright-text {
      max-width: 515px;
      margin-right: auto;
      font-family: inherit;
      text-align: left;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: #ffffff;

      @media (max-width: 575px) {
        max-width: 90%;
        font-size: 10px;
      }
    }
  }
}
