
@import "../../scss/variables";
#comparison {
  padding: 150px 0;
  background: #ffffff;
  font-family: "Montserrat", sans-serif;

  @media (max-width: 991px) {
    padding: 100px 0;
  }

  @media (max-width: 767px) {
    padding: 42px 0 36px;
  }

  .row {
    max-width: 1450px;
    width: 100%;
    overflow: hidden;
  }

  .title-box {
    width: 100%;
    margin-bottom: 46px;

    @media (max-width: 575px) {
      margin-bottom: 20px;
    }

    &__title {
      @media (max-width: 575px) {
        max-width: 320px;
        margin: 0 auto;
        line-height: 24px;
        letter-spacing: 0.01em;
      }

      span {
        color: $primary-color;
      }
    }
  }

  .comparison-swiper {
    position: relative;
    width: 100%;

    @media (max-width: 575px) {
      padding: 0 5px;
    }

    .lists-box {
      @media (min-width: 576px) {
        display: flex;
        justify-content: space-between;
        width: 100%;
        column-gap: 32px;
      }

      @media (max-width: 1023px) and (min-width: 576px) {
        justify-content: center;
        flex-wrap: wrap;
        row-gap: 50px;
      }

      &__item {
        @media (min-width: 576px) {
          display: flex;
          align-items: flex-start;
          width: auto;
          max-width: calc(50% - 16px);
        }

        @media (max-width: 1023px) and (min-width: 576px) {
          max-width: 100%;

          &:nth-child(2) {
            flex-direction: row-reverse;
          }
        }
      }

      &__img-wrap {
        display: flex;
        justify-content: flex-start;
        max-width: 322px;
        width: 322px;

        @media (max-width: 575px) {
          max-width: 100%;
          width: 46.25vw;
          margin: 0 auto;
        }

        img {
          max-width: 100%;

          @media (max-width: 575px) {
            aspect-ratio: 299/439;
          }
        }
      }

      &__content-wrap {
        padding-top: 52px;

        @media (max-width: 575px) {
          padding-top: 5px;
          width: max-content;
          max-width: 100%;
          margin: 0 auto;
        }
      }

      &__item-title {
        margin-bottom: 18px;
        font-weight: 700;
        font-size: 30px;
        line-height: 37px;
        color: $primary-color;

        @media (max-width: 575px) {
          margin-bottom: 13px;
          font-weight: 500;
          font-size: 22px;
          line-height: 27px;
        }

        &_gray {
          color: $text-black-gray;
        }
      }

      &__item-list {
        li {
          position: relative;
          margin-bottom: 19px;
          padding-left: 32px;
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          color: $text-black-gray;

          @media (max-width: 575px) {
            margin-bottom: 10px;
            font-size: 16px;
            line-height: 20px;
          }

          &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 24px;
            height: 24px;
            background-image: url("../images/global/check-circle.svg");
            background-position: center;
            background-repeat: no-repeat;
          }
        }

        &_purchased {
          li {
            &::before {
              background-image: url("../images/modal/modal-close-btn.svg");
            }
          }
        }
      }
    }

    .comparison-pagination {
      display: flex;
      justify-content: center;
      margin-top: 20px;
      width: 100%;
      z-index: 2;

      @media (min-width: 576px) {
        display: none;
      }

      span {
        width: 20px;
        height: 20px;
        margin: 0 6px;
        background-color: #bdbdbd;
        opacity: 1;

        &.swiper-pagination-bullet-active {
          background-color: $primary-color;
        }
      }
    }

    .comparison-navigation {
      position: absolute;
      left: 50%;
      top: 30vw;
      transform: translateX(-50%);
      z-index: 2;
      width: calc(100% - 10px);
      padding: 0;
      display: flex;
      justify-content: space-between;
      pointer-events: none;
      @media (min-width: 576px) {
        display: none;
      }

      .comparison-btn {
        position: relative;
        width: 28px;
        height: 28px;
        background-position: center;
        background-size: 28px 28px;
        background-repeat: no-repeat;
        cursor: pointer;
        pointer-events: all;
        transition: 0.3s all;
      }

      .comparison-btn_next {
        background-image: url("../images/global/slider-next.svg");
      }
      .comparison-btn_prev {
        background-image: url("../images/global/slider-prev.svg");
      }

      .swiper-button-disabled {
        filter: grayscale(1);
      }
    }
  }
}
