
@import "../../scss/variables";
#delivery {
  padding: 150px 0 115px;
  background: #ffffff;

  @media (max-width: 991px) {
    padding: 80px 0 80px;
  }

  @media (max-width: 767px) {
    padding: 60px 0 60px;
  }

  @media (max-width: 575px) {
    padding: 26px 0 46px;
  }

  .title-box {
    margin-bottom: 40px;

    @media (max-width: 575px) {
      margin-bottom: 30px;
    }
  }

  .row {
    display: flex;
    column-gap: 40px;
    width: 100%;
    font-family: "Inter", sans-serif;

    @media (max-width: 767px) {
      flex-direction: column;
      align-items: center;
    }
  }

  .form-container {
    width: 100%;
    max-width: 100%;
    padding: 0;

    @media (max-width: 1399px) {
      position: sticky;
      top: 130px;
    }

    @media (max-width: 767px) {
      position: static;
    }
  }

  .delivery {
    &__title {
      width: 100%;
      margin-bottom: 17px;
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 0.01em;
      color: $text-dark;

      @media (max-width: 767px) {
        margin-bottom: 20px;
      }

      @media (max-width: 575px) {
        font-size: 16px;
        line-height: 20px;
      }
    }

    &__form-wrap {
      flex-direction: column;
      display: flex;
      align-items: center;
      flex-basis: 510px;
      max-width: 32%;

      @media (max-width: 1399px) {
        max-width: 42%;
      }

      @media (max-width: 991px) {
        max-width: 46%;
      }

      @media (max-width: 767px) {
        flex-basis: auto;
        max-width: 100%;
        width: 100%;
      }
    }

    &__form {
      flex-direction: column;
      display: flex;
      align-items: center;
      width: 100%;
      max-width: 100%;
    }

    &__inputs-box {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      row-gap: 24px;
      width: 100%;
      margin-bottom: 0;

      @media (max-width: 575px) {
        row-gap: 20px;
      }

      .delivery__label {
        display: flex;
        flex-direction: column;
        align-items: stretch;

        span {
          margin-bottom: 11px;
          font-family: "Inter", sans-serif;
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          color: #000000;

          @media (max-width: 575px) {
            margin-bottom: 9px;
            font-size: 16px;
            line-height: 22px;
          }
        }

        input {
          height: 71px;
          padding: 18px 25px;
          margin: 0;
          font-family: "Inter", sans-serif;
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 22px;
          }
        }
      }
    }

    &__form-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 282px;
      min-height: 77px;
      margin-top: 40px;
      padding: 10px 15px;
      border-radius: 20px;
      font-family: inherit;
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      text-align: center;
      letter-spacing: 0.01em;
      text-transform: none;

      @media (max-width: 575px) {
        min-height: 62px;
        margin-top: 27px;
        border-radius: 10px;
      }
    }

    &__features-wrap {
      flex-grow: 1;
      padding-top: 33px;

      @media (max-width: 767px) {
        padding-top: 42px;
      }
    }

    &__features {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      column-gap: 20px;
      row-gap: 40px;
      width: 100%;
      padding: 55px 0;
      margin-bottom: 46px;
      border-top: 1px solid #828282;
      border-bottom: 1px solid #828282;

      @media (max-width: 1399px) {
        flex-direction: column;
        align-items: center;
        flex-wrap: wrap;
      }

      @media (max-width: 767px) {
        padding: 0;
        margin-bottom: 27px;
        border: none;
      }
    }

    &__feature {
      display: flex;
      align-items: center;
      column-gap: 4px;

      @media (max-width: 1399px) {
        width: 100%;
        max-width: 320px;
      }

      @media (max-width: 575px) {
        &:nth-child(1) {
          .delivery__feature-icon {
            padding-left: 4px;
          }
        }

        &:nth-child(2) {
          .delivery__feature-icon {
            padding-right: 8px;
          }
        }

        &:nth-child(3) {
          .delivery__feature-icon {
            padding-right: 8px;
            padding-left: 12px;
          }
        }
      }
    }

    &__feature-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 75px;

      img {
        max-width: 100%;
      }

      @media (max-width: 575px) {
        min-width: 57px;
        max-width: 57px;
      }
    }

    &__feature-title {
      margin-bottom: 8px;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 0.01em;
      color: $primary-color;

      @media (max-width: 575px) {
        font-size: 18px;
        line-height: 22px;
      }
    }

    &__feature-text {
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: #828282;

      @media (max-width: 575px) {
        font-size: 16px;
        line-height: 22px;
      }
    }

    &__features-imgs {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      column-gap: 40px;
      row-gap: 27px;
      flex-wrap: wrap;
      width: 100%;

      @media (max-width: 767px) {
        column-gap: 54px;
      }
    }
  }
}
