
@import "../../../scss/variables";
#callme.callme-new {
  display: none;
  width: 100%;
  max-width: 504px;
  padding: 45px 18px 19px 18px !important;
  background: #ffffff;
  border-radius: 3px;
  font-family: "Montserrat", sans-serif;

  .callme-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 390px;
    margin: 0 auto;
  }

  .callme__title {
    margin: 0 0 20px;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    text-transform: none;
    color: #000000;
  }

  .callme__text {
    margin: 0;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #323232;

    @media (max-width: 575px) {
      font-size: 12px;
      line-height: 15px;
    }
  }

  .form-container {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-top: 29px;

    @media (max-width: 575px) {
      margin-top: 3px;
    }
  }

  .call-modal {
    flex-direction: column;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 100%;
  }

  .callme__inputs-box {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    row-gap: 24px;
    width: 100%;
    margin-bottom: 24px;

    @media (max-width: 575px) {
    }

    .callme__label {
      display: flex;
      flex-direction: column;
      align-items: stretch;

      span {
        margin-bottom: 9px;
        font-family: "Montserrat", sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        color: #000000;

        @media (max-width: 575px) {
        }
      }

      input {
        height: auto;
        padding: 18px 25px;
        margin: 0;
        font-family: "Montserrat", sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
      }
    }
  }

  .callme__form-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 263px;
    min-height: 63px;
    padding: 10px 15px;
    font-family: inherit;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.01em;
    text-transform: none;
  }

  .callme__info {
    max-width: 280px;
    margin: 15px auto 0;
    font-family: inherit;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    color: #2f2f2f;
    opacity: 0.9;

    @media (max-width: 575px) {
      margin: 13px auto 0;
    }

    a {
      color: $primary-color;
      transition: 0.3s all;

      &:hover {
        color: $btnred;
      }
    }
  }
}
