#rassrochka {
  padding: 72px 0 66px;
  background: #ffffff;
  overflow: hidden;
}
@media (max-width: 767px) {
  #rassrochka {
    padding: 0;
  }
}
#rassrochka .content-container {
  max-width: 1600px;
  width: 100%;
  padding: 0 !important;
}
#rassrochka .title-box {
  position: relative;
  z-index: 2;
  width: 100%;
  min-height: 233px;
  padding: 50px 220px 100px;
  border-radius: 10px;
  background-color: #fceb0b;
  background-image: url("../images/rassrochka/percent.png");
  background-position: right bottom;
  background-repeat: no-repeat;
  overflow: hidden;
}
@media (max-width: 991px) {
  #rassrochka .title-box {
    min-height: auto;
    padding: 50px 22.2vw 100px;
  }
}
@media (max-width: 767px) {
  #rassrochka .title-box {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding: 20px 20px 64px;
    border-radius: 0;
    background-image: none;
  }
}
#rassrochka .title-box__title {
  position: relative;
  z-index: 2;
  color: #323232;
}
@media (max-width: 575px) {
  #rassrochka .title-box__title {
    max-width: 220px;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
  }
}
#rassrochka .title-box__img {
  position: absolute;
  z-index: 1;
  top: 41px;
  left: 4.3%;
}
@media (max-width: 991px) {
  #rassrochka .title-box__img {
    top: calc(50% - 10px);
    left: 4%;
    transform: translateY(-50%);
    max-width: 157px;
    width: 18vw;
  }
}
@media (max-width: 767px) {
  #rassrochka .title-box__img {
    position: static;
    transform: translateY(0);
    width: auto;
  }
}
@media (max-width: 575px) {
  #rassrochka .title-box__img {
    width: 74px;
  }
}
#rassrochka .title-box__img img {
  width: 100%;
}
#rassrochka .features-box {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  column-gap: 20px;
  max-width: 1050px;
  padding: 0 16px;
  width: 100%;
  margin-top: -83px;
}
@media (max-width: 767px) {
  #rassrochka .features-box {
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    margin-top: -44px;
    padding: 0 28px;
  }
}
@media (max-width: 575px) {
  #rassrochka .features-box {
    row-gap: 10px;
  }
}
#rassrochka .features-box__item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 1px;
  flex-grow: 1;
  min-height: 226px;
  padding: 10px;
  background: #f2f2f2;
  border-radius: 10px;
}
@media (max-width: 767px) {
  #rassrochka .features-box__item {
    width: 100%;
    min-height: 87px;
    max-width: 500px;
    padding: 14px 12px 10px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
  }
}
#rassrochka .features-box__title {
  margin-bottom: 10px;
  font-weight: 600;
  font-size: 30px;
  line-height: 37px;
  text-align: center;
  color: #323232;
}
@media (max-width: 575px) {
  #rassrochka .features-box__title {
    margin-bottom: 1px;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
  }
}
#rassrochka .features-box__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #323232;
}
@media (max-width: 575px) {
  #rassrochka .features-box__text {
    font-size: 18px;
    line-height: 22px;
  }
}
#rassrochka .info-box {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: stretch;
  column-gap: 20px;
  max-width: 1050px;
  padding: 0 16px;
  width: 100%;
  margin-top: 25px;
}
@media (max-width: 767px) {
  #rassrochka .info-box {
    flex-direction: column-reverse;
    padding: 0;
    margin-top: 0;
  }
}
#rassrochka .info-box__content-wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  max-width: 600px;
  padding: 36px 45px 40px;
  background: #f2f2f2;
  border-radius: 10px;
}
@media (max-width: 991px) {
  #rassrochka .info-box__content-wrap {
    width: 58%;
    padding: 36px 40px 43px;
  }
}
@media (max-width: 767px) {
  #rassrochka .info-box__content-wrap {
    align-items: center;
    max-width: 100%;
    width: 100%;
    padding: 27px 12px 20px;
    border-radius: 0;
  }
}
#rassrochka .info-box__list {
  width: 100%;
  max-width: 380px;
  margin-bottom: 42px;
  font-family: "Inter", sans-serif;
}
@media (max-width: 767px) {
  #rassrochka .info-box__list {
    margin-bottom: 0;
  }
}
#rassrochka .info-box__list-item {
  display: flex;
  align-items: flex-start;
  column-gap: 10px;
  width: 100%;
}
#rassrochka .info-box__list-item:not(:last-child) {
  padding-bottom: 15px;
  border-bottom: 1px solid #bdbdbd;
  margin-bottom: 15px;
}
@media (max-width: 767px) {
  #rassrochka .info-box__list-item {
    flex-direction: column;
    align-items: center;
    row-gap: 5px;
  }
  #rassrochka .info-box__list-item:not(:last-child) {
    padding-bottom: 12px;
    margin-bottom: 12px;
  }
}
@media (max-width: 575px) {
  #rassrochka .info-box__list-item:first-child .info-box__list-text {
    max-width: 220px;
  }
}
#rassrochka .info-box__list-number {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #323232;
}
@media (max-width: 767px) {
  #rassrochka .info-box__list-number {
    text-align: center;
  }
}
#rassrochka .info-box__list-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.7);
}
#rassrochka .info-box__list-text span {
  font-weight: 700;
}
@media (max-width: 767px) {
  #rassrochka .info-box__list-text {
    text-align: center;
  }
}
#rassrochka .info-box__price {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  color: #323232;
}
@media (max-width: 991px) {
  #rassrochka .info-box__price {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 1px;
  }
}
@media (max-width: 767px) {
  #rassrochka .info-box__price {
    align-items: center;
    margin-top: 20px;
    order: 3;
    text-align: center;
  }
}
@media (max-width: 575px) {
  #rassrochka .info-box__price {
    font-size: 30px;
    line-height: 37px;
  }
}
#rassrochka .info-box__price span {
  margin-right: 11px;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
  text-decoration-line: line-through;
  color: #828282;
}
@media (max-width: 767px) {
  #rassrochka .info-box__price span {
    margin: 0 auto;
  }
}
@media (max-width: 575px) {
  #rassrochka .info-box__price span {
    font-size: 20px;
    line-height: 24px;
  }
}
#rassrochka .info-box__btn {
  position: relative;
  z-index: 4;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 380px;
  min-height: 77px;
  padding: 10px 20px;
  margin-top: 24px;
  background: #fceb0a;
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.01em;
  color: #323232;
  transition: 0.3s all;
  cursor: pointer;
}
@media (max-width: 767px) {
  #rassrochka .info-box__btn {
    max-width: 500px;
  }
}
@media (max-width: 575px) {
  #rassrochka .info-box__btn {
    min-height: 65px;
    max-width: 267px;
    padding: 10px;
    border-radius: 10px;
    font-size: 20px;
    line-height: 24px;
  }
}
#rassrochka .info-box__btn:hover {
  background: rgb(214, 200, 3);
  box-shadow: 0 4px 4px 1px rgba(214, 200, 3, 0.32);
  transform: translateY(-2px);
}
#rassrochka .info-box__img-wrap {
  position: relative;
  flex-grow: 1;
  z-index: 3;
}
@media (max-width: 767px) {
  #rassrochka .info-box__img-wrap {
    margin-top: -10px;
  }
}
#rassrochka .info-box__img-absolute {
  position: absolute;
  left: -140px;
  bottom: -66px;
  width: 70vw;
  max-width: 883px;
  height: auto;
  transition: 0.3s all;
}
@media (max-width: 1023px) {
  #rassrochka .info-box__img-absolute {
    width: 74vw;
  }
}
@media (max-width: 991px) {
  #rassrochka .info-box__img-absolute {
    width: 75vw;
  }
}
@media (max-width: 767px) {
  #rassrochka .info-box__img-absolute {
    position: static;
    width: auto;
    max-width: 100vw;
  }
}
#rassrochka .info-box__img-absolute picture {
  display: flex;
}
#rassrochka .info-box__img-absolute img {
  width: 100%;
  max-width: 883px;
  transition: 0.3s all;
}