#instant {
  padding: 75px 0 102px;
  background: #f3f3f3;
}
@media (max-width: 767px) {
  #instant {
    padding: 70px 0 70px;
  }
}
@media (max-width: 575px) {
  #instant {
    padding: 20px 0 0;
  }
}
#instant .fade-enter-active,
#instant .fade-leave-active {
  transition: opacity 0.4s ease;
}
#instant .fade-enter-from,
#instant .fade-leave-to {
  opacity: 0 !important;
}
#instant .grid-box {
  display: grid;
  grid-template-columns: 350px 460px 1fr;
  grid-template-rows: auto;
  width: 100%;
  align-items: start;
}
@media (max-width: 1199px) {
  #instant .grid-box {
    grid-template-columns: 350px 460px;
    justify-content: center;
  }
}
@media (max-width: 991px) {
  #instant .grid-box {
    grid-template-columns: 350px 1fr;
  }
}
@media (max-width: 767px) {
  #instant .grid-box {
    grid-template-columns: 1fr;
  }
}
#instant .title-box {
  position: relative;
  z-index: 2;
  grid-column: 1/-1;
  grid-row: 1/2;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  #instant .title-box {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin-bottom: 0;
    padding-top: 25px;
  }
  #instant .title-box::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    height: 1px;
    background: rgba(0, 0, 0, 0.25);
  }
}
#instant .instant__img-wrap {
  display: grid;
  grid-template-columns: 1fr;
  grid-column: 1/2;
  grid-row: 2/3;
  margin-top: -40px;
}
@media (max-width: 1599px) {
  #instant .instant__img-wrap {
    position: sticky;
    top: 120px;
  }
}
@media (max-width: 1199px) {
  #instant .instant__img-wrap {
    position: static;
  }
}
@media (max-width: 767px) {
  #instant .instant__img-wrap {
    grid-column: 1/-1;
    grid-row: 3/4;
    margin-top: 0;
    margin-bottom: 3px;
  }
}
#instant .instant__img-wrap .instant__img-box {
  grid-row: 1/2;
  grid-column: 1/2;
  position: relative;
  min-height: 625px;
  width: 100%;
  flex-shrink: 0;
}
@media (max-width: 767px) {
  #instant .instant__img-wrap .instant__img-box {
    min-height: auto;
    height: 520px;
  }
}
@media (max-width: 575px) {
  #instant .instant__img-wrap .instant__img-box {
    height: 375px;
  }
}
#instant .instant__img-wrap .instant__img-box img {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  max-height: 100%;
  object-fit: contain;
}
@media (max-width: 767px) {
  #instant .instant__img-wrap .instant__img-box img {
    width: auto;
    left: 50%;
    transform: translateX(-50%);
  }
}
#instant .instant__product-wrap {
  grid-column: 2/3;
  grid-row: 2/3;
  min-height: 585px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 20px;
}
@media (max-width: 1599px) {
  #instant .instant__product-wrap {
    position: sticky;
    top: 160px;
  }
}
@media (max-width: 1199px) {
  #instant .instant__product-wrap {
    position: static;
  }
}
@media (max-width: 767px) {
  #instant .instant__product-wrap {
    grid-column: 1/-1;
    grid-row: 4/5;
    min-height: auto;
    padding-bottom: 0;
  }
}
@media (max-width: 575px) {
  #instant .instant__product-wrap {
    margin: 0 -20px;
  }
}
#instant .instant__product-wrap .instant__product-togglers {
  position: relative;
  display: flex;
  align-items: stretch;
  justify-content: flex-end;
  column-gap: 5px;
  width: 100%;
  padding-right: 20px;
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__product-togglers {
    justify-content: center;
    padding-right: 0;
  }
}
#instant .instant__product-wrap .instant__product-toggler {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 73px;
  min-height: 54px;
  border-radius: 5px 5px 0px 0px;
  background: rgba(255, 255, 255, 0.5);
  color: #828282;
  font-weight: 700;
  font-family: "Inter", sans-serif;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.01em;
  cursor: pointer;
  transition: 0.3s all;
}
@media (min-width: 992px) {
  #instant .instant__product-wrap .instant__product-toggler:hover {
    background: #ffffff;
  }
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__product-toggler {
    width: 70px;
  }
}
#instant .instant__product-wrap .instant__product-toggler_active {
  background: #ffffff;
  color: #f82c2c;
}
#instant .instant__product-wrap .instant__product-info {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 16px 30px 26px 24px;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.15);
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__product-info {
    padding: 40px 16px 30px;
    border-radius: 0;
  }
}
#instant .instant__product-wrap .instant__oldprice-wrap {
  display: flex;
  justify-content: start;
  align-items: center;
  column-gap: 12px;
  margin-bottom: 20px;
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__oldprice-wrap {
    column-gap: 4;
  }
}
#instant .instant__product-wrap .instant__discont {
  position: relative;
  padding: 0 6px;
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__discont {
    padding: 0 28px 0 10px;
  }
}
#instant .instant__product-wrap .instant__discont span {
  position: relative;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}
#instant .instant__product-wrap .instant__discont::before {
  content: "";
  position: absolute;
  left: 0;
  top: -25px;
  width: 100%;
  height: 61px;
  background: url("../images/instant/rectangle.svg");
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
@media (max-width: 991px) {
  #instant .instant__product-wrap .instant__discont::before {
    top: -17px;
    height: 55px;
  }
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__discont::before {
    left: -16px;
    top: 50%;
    transform: translateY(-50%);
    width: 95px;
    height: 55px;
    background: url("../images/instant/rectangle-mobil.svg");
    background-position: left center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
}
#instant .instant__product-wrap .instant__oldprice {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: line-through;
  color: #4f4f4f;
}
#instant .instant__product-wrap .instant__price {
  margin-bottom: 15px;
  font-weight: 800;
  font-size: 40px;
  line-height: 49px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__price {
    margin-bottom: 10px;
  }
}
#instant .instant__product-wrap .instant__present-wrap {
  display: flex;
  align-items: center;
  column-gap: 9px;
  margin-bottom: 26px;
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__present-wrap {
    margin-bottom: 14px;
  }
}
#instant .instant__product-wrap .instant__present-img {
  max-width: 43px;
}
#instant .instant__product-wrap .instant__present-title {
  margin-bottom: 1px;
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #f82c2c;
}
#instant .instant__product-wrap .instant__present-text {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
#instant .instant__product-wrap .instant__btn {
  width: 100%;
  min-height: 64px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 0.01em;
}
@media (max-width: 575px) {
  #instant .instant__product-wrap .instant__btn {
    border-radius: 10px;
  }
}
#instant .instant__cards-wrap {
  grid-column: 3/-1;
  grid-row: 2/3;
  padding-left: 42px;
  margin-bottom: 20px;
}
@media (max-width: 1199px) {
  #instant .instant__cards-wrap {
    grid-column: 1/-1;
    grid-row: 3/4;
    padding-left: 0;
    padding-top: 40px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  #instant .instant__cards-wrap {
    grid-column: 1/-1;
    grid-row: 1/2;
    width: 100%;
    overflow: hidden;
    padding-top: 0;
    margin-bottom: 34px;
  }
}
#instant .instant__cards-wrap .instant__cards-title {
  margin-bottom: 22px;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}
@media (min-width: 767px) {
  #instant .instant__cards-wrap .instant__cards-title {
    margin-bottom: 19px;
  }
}
#instant .instant__cards-wrap .instant-swiper {
  width: 100%;
}
@media (max-width: 767px) {
  #instant .instant__cards-wrap .instant-swiper {
    max-width: 450px;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
  }
}
#instant .instant__cards-wrap .instant-pagination {
  display: flex;
  justify-content: center;
  margin-top: 27px;
  padding-bottom: 4px;
  padding-left: 30px;
  padding-right: 30px;
  width: 100%;
  z-index: 2;
}
@media (min-width: 767px) {
  #instant .instant__cards-wrap .instant-pagination {
    display: none;
  }
}
#instant .instant__cards-wrap .instant-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
}
#instant .instant__cards-wrap .instant-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#instant .instant__cards-wrap .instant-navigation {
  position: absolute;
  bottom: 0;
  z-index: 2;
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 767px) {
  #instant .instant__cards-wrap .instant-navigation {
    display: none;
  }
}
#instant .instant__cards-wrap .instant-navigation .instant-btn {
  position: relative;
  width: 28px;
  height: 28px;
  background-position: center;
  background-size: 28px 28px;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#instant .instant__cards-wrap .instant-navigation .instant-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#instant .instant__cards-wrap .instant-navigation .instant-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#instant .instant__cards-wrap .instant-navigation .swiper-button-disabled {
  filter: grayscale(1);
}
@media (min-width: 768px) {
  #instant .instant__cards-wrap .instant__cards-box {
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: wrap;
    row-gap: 28px;
    column-gap: 30px;
  }
}
#instant .instant__cards-wrap .instant__card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: calc(33.3333333333% - 20px);
  min-height: 240px;
  padding: 37px 20px 20px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
}
@media (max-width: 1599px) {
  #instant .instant__cards-wrap .instant__card {
    width: calc(50% - 15px);
  }
}
@media (max-width: 1399px) {
  #instant .instant__cards-wrap .instant__card {
    width: 100%;
  }
}
@media (max-width: 1199px) {
  #instant .instant__cards-wrap .instant__card {
    width: calc(33.3333333333% - 20px);
  }
}
#instant .instant__cards-wrap .instant__card-img {
  width: 150px;
  height: 136px;
}
#instant .instant__cards-wrap .instant__card-text {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}