
@import "../../scss/variables";
#features {
  padding: 149px 0 0;
  border-top: 1px solid #bdbdbd;
  overflow: hidden;

  @media (max-width: 991px) {
    padding: 100px 0 0;
  }

  @media (max-width: 767px) {
    padding: 80px 0 0;
  }

  @media (max-width: 575px) {
    padding: 30px 0 0;
  }

  .title-box {
    margin-bottom: 60px;

    @media (max-width: 575px) {
      margin-bottom: 27px;
    }
  }

  .features {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;

    .feature {
      display: flex;
      align-items: stretch;
      justify-content: flex-start;
      width: 100%;

      @media (max-width: 1399px) {
        flex-direction: column;
      }

      &:nth-child(2n) {
        flex-direction: row-reverse;

        @media (max-width: 1399px) {
          flex-direction: column;
        }
      }

      &__img-wrap {
        display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 50%;
        flex-shrink: 0;
        &:not(.feature__img-wrap_slider) {
          background-color: #e7e7e7;
        }

        @media (max-width: 1399px) {
          width: 100%;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;

          @media (max-width: 1399px) {
            max-height: 600px;
          }
        }
      }

      &__img {
        width: 100%;
      }

      &__slider {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;

        @media (max-width: 575px) {
          overflow: visible;
          margin-bottom: 50px;
        }

        .swiper-slide {
          height: auto;
          picture {
            height: 100%;
          }

          img {
            object-fit: cover;
          }
        }

        .feature-pagination {
          position: absolute;
          bottom: 34px;
          left: 50%;
          transform: translateX(-50%);
          display: flex;
          justify-content: center;
          margin-top: 116px;
          width: 100%;
          z-index: 2;

          @media (max-width: 575px) {
            bottom: -50px;
            justify-content: flex-start;
            width: 100%;
            padding: 0 20px;
            margin-top: 19px;
          }

          span {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 42px;
            height: 42px;
            margin: 0 19px;
            background: rgba(255, 255, 255, 0.7);
            border: 2px solid $primary-color;
            color: $primary-color;
            opacity: 1;
            transition: 0.3s all;
            font-family: "Inter", sans-serif;
            font-weight: 700;
            font-size: 20px;
            line-height: 24px;
            letter-spacing: 0.01em;
            @media (min-width: 768px) {
              &:hover {
                background-color: $primary-color;
                color: #ffffff;
              }
            }

            @media (max-width: 575px) {
              width: 31px;
              height: 31px;
              margin: 0 18px 0 0;
            }

            &.swiper-pagination-bullet-active {
              background-color: $primary-color;
              color: #ffffff;
            }
          }
        }
      }

      &__text-wrap {
        width: 100%;
        max-width: 910px;
        padding: 35px 50px;
        font-family: "Inter", sans-serif;

        @media (max-width: 1399px) {
          margin: 0 auto;
        }

        @media (max-width: 767px) {
          padding: 35px 20px;
        }

        @media (max-width: 575px) {
          padding: 9px 20px 35px;
        }
      }

      &__title {
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        color: $primary-color;

        @media (max-width: 575px) {
          font-size: 24px;
          line-height: 29px;
        }
      }

      &__subtitle {
        margin-bottom: 30px;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        color: $text-black-gray;

        @media (max-width: 575px) {
          display: none;
        }
      }

      &__text {
        margin-bottom: 15px;
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        color: $text-black-gray;

        @media (max-width: 575px) {
          font-size: 16px;
          line-height: 22px;

          br {
            display: none;
          }
        }
      }

      &__list-title {
        margin-bottom: 15px;
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        color: $text-black-gray;

        @media (max-width: 575px) {
          font-size: 16px;
          line-height: 22px;
        }
      }

      &__list {
        display: flex;
        flex-direction: column;
        padding-top: 5px;
        row-gap: 10px;
        width: 100%;
      }

      &__list-item {
        display: flex;
        align-items: flex-start;
        column-gap: 10px;
      }

      &__list-number {
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        color: $primary-color;

        @media (max-width: 575px) {
          font-size: 16px;
          line-height: 22px;
        }
      }

      &__list-text {
        font-weight: 300;
        font-size: 20px;
        line-height: 28px;
        color: $text-black-gray;

        @media (max-width: 575px) {
          font-weight: 400;
          font-size: 16px;
          line-height: 22px;
        }

        span {
          font-weight: 700;
        }
      }

      &__btn-box {
        display: flex;
        align-items: center;
        column-gap: 42px;
        margin-top: 40px;

        @media (max-width: 767px) {
          flex-direction: column-reverse;
          row-gap: 18px;
          margin-top: 20px;
        }
      }

      &__btn {
        min-height: 77px;
        max-width: 278px;
        width: 100%;
        padding: 10px;
        border-radius: 20px;
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
        color: #ffffff;

        @media (max-width: 575px) {
          min-height: 55px;
          max-width: 268px;
          border-radius: 10px;
          font-size: 18px;
          line-height: 22px;
        }
      }

      &__price {
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        color: $btnred;

        @media (max-width: 575px) {
          font-size: 28px;
          line-height: 34px;
        }
      }
    }
  }
}
