#header-features {
  padding: 154px 0;
  background: #ffffff;
  border-bottom: 1px solid #bdbdbd;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 991px) {
  #header-features {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  #header-features {
    padding: 45px 0 55px;
  }
}
@media (max-width: 1199px) {
  #header-features .content-container {
    max-width: 1000px;
  }
}
#header-features .header-features__title-wrap {
  width: 100%;
  margin-bottom: 55px;
}
@media (max-width: 479px) {
  #header-features .header-features__title-wrap {
    margin-bottom: 28px;
  }
}
#header-features .header-features__main-title {
  margin: 0;
  font-weight: 600;
  font-size: 50px;
  line-height: 61px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  text-transform: none;
  color: #4f4f4f;
}
@media (max-width: 767px) {
  #header-features .header-features__main-title {
    font-size: 30px;
    line-height: 37px;
  }
}
@media (max-width: 479px) {
  #header-features .header-features__main-title {
    text-align: left;
  }
}
#header-features .header-features__row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  margin: 0 -15px;
}
@media (max-width: 1559px) {
  #header-features .header-features__row {
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 30px;
    max-width: 760px;
  }
}
@media (max-width: 479px) {
  #header-features .header-features__row {
    justify-content: flex-start;
  }
}
#header-features .header-features__item {
  display: flex;
  align-items: flex-start;
  width: 100%;
  max-width: 380px;
  padding: 0 15px;
}
#header-features .header-features__img-box {
  flex-shrink: 0;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 65px;
  margin-right: 5px;
}
@media (max-width: 767px) {
  #header-features .header-features__img-box {
    width: 52px;
    margin-right: 17px;
  }
}
#header-features .header-features__img-box img {
  object-fit: contain;
  max-width: 100%;
}
@media (max-width: 767px) {
  #header-features .header-features__img-box img {
    max-width: 52px;
    width: 100%;
    height: auto;
  }
}
#header-features .header-features__text-box {
  flex-grow: 1;
}
#header-features .header-features__title {
  margin-bottom: 5px;
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #007aeb;
}
@media (max-width: 575px) {
  #header-features .header-features__title {
    font-size: 16px;
    line-height: 20px;
  }
}
#header-features .header-features__subtitle {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
  margin-bottom: 0;
}
@media (max-width: 575px) {
  #header-features .header-features__subtitle {
    font-size: 16px;
    line-height: 19px;
  }
}