
@import "../../../scss/variables";
#constructor-new {
  display: none;
  padding: 0;
  background: #ffffff;
  border-radius: 10px;
  max-width: 1620px;
  width: 100%;
  border: 2px solid #828282;
  font-family: "Montserrat", sans-serif !important;

  @media (max-width: 991px) {
    max-width: 500px;
    padding: 0 !important;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.4s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0 !important;
  }

  .constructor-new__wrap {
    display: flex;
    align-items: stretch;
    width: 100%;
    height: 100%;
    cursor: default;

    @media (max-width: 991px) {
      flex-direction: column;
    }
  }

  .constructor-new__img-wrap {
    width: 100%;
    max-width: 585px;
    background: #f2f2f2;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;

    @media (max-width: 1499px) {
      max-width: 485px;
    }

    @media (max-width: 1365px) {
      max-width: 585px;
    }

    @media (max-width: 1199px) {
      display: flex;
      justify-content: center;
      max-width: 485px;
    }

    @media (max-width: 991px) {
      max-width: 100%;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      border-bottom-left-radius: 0;
    }

    .constructor-new__img-box {
      position: fixed;
      top: 50%;
      transform: translateY(-50%);
      aspect-ratio: 0.65;
      width: 100%;
      max-width: 585px;
      pointer-events: none;
      overflow: hidden;

      @media (max-width: 1499px) {
        max-width: 485px;
      }

      @media (max-width: 1365px) {
        max-width: 585px;
      }

      @media (max-width: 1199px) {
        max-height: 100vh;
        max-width: 485px;
      }

      @media (max-width: 991px) {
        position: relative;
        top: 0;
        max-height: auto;
        aspect-ratio: 0.64646;
        transform: translateY(0);
        max-width: 100%;
      }

      img {
        position: absolute;
      }

      .img-dop {
        position: absolute;
        opacity: 0.4;
        transition: 0.3s all;

        &_show {
          opacity: 1;
        }

        img {
          position: static;
          max-width: 100%;
          height: auto;
        }
      }

      .main-product {
        position: absolute;
        display: flex;
        justify-content: flex-start;
        width: 63.07%;
        height: 87.22%;
        top: 0;
        left: 15%;

        img {
          max-width: 100%;
        }

        @media (max-width: 991px) {
          align-items: center;
          width: 56.5625%;
          height: 79.22%;
          // top: 5.252%;
          left: 21.1875%;
        }
      }

      .arom-dop {
        width: 27%;
        bottom: 2.111%;
        right: 25.47%;
        @media (max-width: 991px) {
          width: 23.3437%;
          bottom: 10.836%;
          right: 32.03%;
        }
      }

      .dioptr-dop {
        width: 22.375%;
        bottom: 10%;
        right: 21.36%;
        @media (max-width: 991px) {
          width: 19.6875%;
          bottom: 0;
          right: 20.625%;
        }
      }

      .kolonna-dop {
        width: 20.683%;
        right: 21.4188%;
        bottom: 28.666%;
        @media (max-width: 991px) {
          width: 17.8125%;
          bottom: 36.5656%;
          right: 6.562%;
        }
      }

      .popugai-dop {
        width: 28.376%;
        right: -0.6666%;
        bottom: 2.7777%;
        @media (max-width: 991px) {
          width: 24.375%;
          bottom: 11.313%;
          right: 9.062%;
        }
      }

      .spn-m-dop {
        width: 39.83%;
        left: -1%;
        bottom: 7.666%;
        @media (max-width: 991px) {
          width: 34.375%;
          bottom: 10.5%;
          left: 9.68%;
        }
      }

      .spn-n-dop {
        width: 46.8376%;
        bottom: 0.6666%;
        left: 5.983%;
        @media (max-width: 991px) {
          width: 40.625%;
          bottom: 4.444%;
          left: 15.9375%;
        }
      }

      .zarga-dop {
        width: 19.145%;
        bottom: 21.8888%;
        left: 0.906%;
        @media (max-width: 991px) {
          width: 16.5625%;
          bottom: 21.2121%;
          left: 6.875%;
        }
      }
    }
  }

  .constructor-new__content-wrap {
    max-width: calc(100% - 585px);
    width: 100%;
    padding: 40px 80px 17px 60px;

    @media (max-width: 1499px) {
      max-width: calc(100% - 485px);
    }

    @media (max-width: 1365px) {
      max-width: calc(100% - 585px);
      padding: 40px 80px 32px 60px;
    }

    @media (max-width: 1199px) {
      max-width: calc(100% - 485px);
      padding: 32px 50px 32px 40px;
    }

    @media (max-width: 991px) {
      max-width: 100%;
      padding: 32px;
    }

    @media (max-width: 575px) {
      padding: 15px 11px 29px;
    }

    .constructor-new__content-text {
      width: 100%;
      margin-bottom: 42px;

      @media (max-width: 991px) {
        margin-bottom: 14px;
      }

      .constructor-new__title {
        margin: 0 0 10px;
        font-weight: 600;
        font-size: 50px;
        line-height: 61px;
        font-family: inherit;
        text-transform: none;
        letter-spacing: normal;
        text-align: left;
        color: #4f4f4f;

        @media (min-width: 992px) {
          br {
            display: none;
          }
        }

        @media (max-width: 1365px) {
          text-align: center;
        }

        @media (max-width: 991px) {
          font-weight: 700;
          font-size: 24px;
          line-height: 29px;
        }
      }

      .constructor-new__subtitle {
        width: 100%;
        max-width: 670px;
        font-weight: 500;
        font-size: 24px;
        line-height: 29px;
        letter-spacing: 0.01em;
        font-family: inherit;
        color: #4f4f4f;

        @media (max-width: 1365px) {
          text-align: center;
          margin: 0 auto;
        }

        @media (max-width: 991px) {
          margin-left: auto;
          margin-right: auto;
          font-weight: 300;
          font-size: 12px;
          line-height: 15px;
          max-width: 275px;
        }
      }
    }

    .constructor-new__content-order {
      display: grid;
      grid-template-columns: 1fr 375px;
      grid-template-rows: min-content 1fr;
      width: 100%;
      column-gap: 32px;

      @media (max-width: 1365px) {
        grid-template-columns: 1fr;
        grid-template-rows: auto;
      }

      .constructor-new__block-subtitle {
        margin-bottom: 15px;
        font-weight: 400;
        font-size: 24px;
        line-height: 29px;
        letter-spacing: 0.01em;
        font-family: inherit;
        color: #4f4f4f;

        @media (max-width: 1365px) {
          display: none;
        }
      }
    }

    .constructor-new__content-dops {
      grid-column: 1/2;
      grid-row: 1/3;
      max-width: 432px;

      @media (max-width: 1365px) {
        grid-column: 1/2;
        grid-row: 2/3;
        max-width: 100%;
      }

      .dops-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: stretch;
        width: 100%;
        @media (max-width: 1365px) {
          margin-top: 25px;
        }

        @media (max-width: 575px) {
          margin-top: 20px;
        }
      }

      .dops-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 22px;
        width: 100%;
        padding: 20px 0 23px;

        &:not(:last-child) {
          border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        }

        @media (max-width: 575px) {
          column-gap: 13px;
          padding: 11px 0 16px;
        }
      }

      .dops-item__name {
        font-weight: 700;
        font-size: 18px;
        line-height: 22px;
        display: flex;
        align-items: center;
        letter-spacing: 0.01em;
        text-decoration-line: underline;
        color: $text-dark;
        transition: 0.3s all;

        @media (max-width: 575px) {
          font-size: 14px;
          line-height: 17px;
        }

        &:hover {
          color: #ff0000;
        }
      }

      .dops-item__price-wrap {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-end;
        flex-wrap: wrap;
        margin-left: auto;
      }

      .dops-item__price {
        white-space: nowrap;
        font-weight: 700;
        font-size: 18px;
        line-height: 28px;
        text-align: right;
        color: #333333;

        @media (max-width: 575px) {
          font-size: 14px;
          line-height: 28px;
        }
      }

      .dops-item__oldprice {
        font-family: inherit;
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        text-align: right;
        text-decoration-line: line-through;
        color: #333333;

        @media (max-width: 575px) {
          font-size: 14px;
          line-height: 17px;
        }
      }
    }

    .constructor-new__content-select {
      grid-column: 2/3;
      grid-row: 1/2;

      @media (max-width: 1365px) {
        grid-column: 1/2;
        grid-row: 1/2;
      }

      .constructor-new__custom-select-wrap {
        position: relative;
        width: 100%;
        padding-top: 12px;

        @media (max-width: 575px) {
          padding-top: 0;
        }

        .constructor-new__custom-select {
          position: relative;
          z-index: 2;
          display: flex;
          width: 100%;
          height: 71px;
          border: 1px solid #4f4f4f;
          border-radius: 10px;
          background: #f2f2f2;
          overflow: hidden;
          .constructor-new__custom-select-info {
            flex-grow: 1;
            display: flex;
            justify-content: space-between;
            align-items: center;
            column-gap: 5px;
            padding: 11px 23px 12px 25px;

            @media (max-width: 575px) {
              column-gap: 3px;
              padding: 12px 20px 12px 15px;
            }

            p {
              width: auto;
              max-width: 165px;
              font-weight: 400;
              font-size: 19px;
              line-height: 24px;
              display: flex;
              align-items: center;
              letter-spacing: -0.01em;
              color: rgba(0, 0, 0, 0.5);

              @media (max-width: 575px) {
                font-size: 14px;
                line-height: 17px;
              }

              @media (min-width: 480px) {
                br {
                  display: none;
                }
              }
            }

            .constructor-new__custom-select-indicator {
              font-weight: 700;
              font-size: 30px;
              line-height: 36px;
              display: flex;
              align-items: center;
              color: #000000;
            }
          }

          .constructor-new__custom-select-btn {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
            height: 100%;
            width: 80px;
            border-left: 1px solid #4f4f4f;
            cursor: pointer;
            transition: 0.3s all;

            @media (max-width: 575px) {
              width: 60px;
            }

            svg {
              width: 28px;
              height: 16px;
              transition: 0.3s all;
              path {
                stroke: #4f4f4f;
                transition: 0.3s all;
              }
            }

            &_active {
              background-color: rgba(255, 0, 0, 0.5);
              svg {
                width: 28px;
                height: 16px;
                transform: rotate(180deg);
                path {
                  stroke: #000000;
                }
              }
            }

            &:hover {
              background-color: rgba(255, 0, 0, 0.5);
              svg {
                width: 28px;
                height: 16px;
                path {
                  stroke: #000000;
                }
              }
            }
          }
        }

        .constructor-new__custom-select-list {
          position: absolute;
          top: 72px;
          left: 0;
          display: flex;
          flex-wrap: wrap;
          row-gap: 10px;
          width: 100%;
          padding: 0 15px;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
          background: #f2f2f2;
          overflow: hidden;
          height: 0;
          transform: scaleY(0.75);
          opacity: 0.5;
          border: 1px solid #4f4f4f;
          transition: 0.4s all;
          transform-origin: top;

          @media (max-width: 575px) {
            top: 60px;
            row-gap: 8px;
          }

          &_show {
            height: max-content;
            opacity: 1;
            padding: 20px 15px 15px;
            transform: scaleY(1);
          }

          li {
            position: relative;
            width: 100%;
            padding: 0 5px;
            font-weight: 400;
            font-size: 19px;
            line-height: 24px;
            letter-spacing: -0.01em;
            color: rgba(0, 0, 0, 0.5);
            transition: 0.3s all;
            cursor: pointer;

            @media (max-width: 575px) {
              font-size: 14px;
              line-height: 18px;
            }

            &:before {
              content: "+";
              position: absolute;
              top: 50%;
              left: 0;
              transform: translateY(-50%);
              font-size: 19px;
              color: rgba(0, 0, 0, 0.5);
              opacity: 0;
              transition: 0.3s all;

              @media (max-width: 575px) {
                display: none;
              }
            }

            &:hover {
              padding-left: 24px;
              color: rgba(0, 0, 0, 1);
              background-color: rgba(255, 0, 0, 0.5);

              &:before {
                content: "+";
                left: 7px;
                color: rgba(0, 0, 0, 1);
                opacity: 1;
              }
            }
          }
        }
      }
    }

    .constructor-new__content-form {
      grid-column: 2/3;
      grid-row: 2/3;
      //   width: 375px;

      @media (max-width: 1365px) {
        grid-column: 1/2;
        grid-row: 3/4;
      }

      .constructor-new__form {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        margin-top: 27px;

        @media (max-width: 575px) {
          margin-top: 20px;
        }
      }

      .fixed-box {
        width: 100%;
      }

      .constructor-new__price-box {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        column-gap: 8px;
        margin-bottom: 25px;

        @media (max-width: 575px) {
          flex-direction: column;
          justify-content: flex-start;
          align-items: stretch;
          margin-bottom: 22px;
          row-gap: 19px;
        }

        .constructor-new__price-wrap {
          width: max-content;
          max-width: 100%;

          @media (max-width: 575px) {
            order: 2;
          }

          .constructor-new__price-text {
            font-family: inherit;
            font-weight: 400;
            font-size: 24px;
            line-height: 32px;
            color: #000000;

            @media (max-width: 575px) {
              font-size: 24px;
              line-height: 40px;
            }
          }

          .constructor-new__price-number {
            font-family: inherit;
            font-weight: 700;
            font-size: 40px;
            line-height: 40px;
            color: #000000;
            white-space: nowrap;
          }
        }

        .constructor-new__rassrochka-check {
          position: relative;
          display: flex;
          align-items: center;
          column-gap: 10px;
          width: max-content;
          max-width: 100%;
          cursor: pointer;

          @media (max-width: 575px) {
            order: 1;
          }

          input {
            position: absolute;
            visibility: hidden;
            width: 1px;
            height: 1px;
          }
          div {
            width: 24px;
            height: 24px;
            border: 2px solid #4f4f4f;
            background-position: center;
            background-repeat: no-repeat;
            background-size: 14px 9px;
            transition: 0.3s all;
          }

          span {
            width: max-content;
            max-width: 113px;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            display: flex;
            align-items: center;
            letter-spacing: 0.01em;
            color: #4f4f4f;

            @media (max-width: 575px) {
              max-width: 100%;
              font-size: 14px;
              line-height: 17px;
            }
          }

          &:hover {
            input[type="checkbox"]:not(:checked) + div {
              border-color: #ff0000;
              box-shadow: 0 0 4px 1px rgba(255, 0, 0, 0.3);
            }
          }

          input[type="checkbox"]:checked + div {
            background-image: url("../images/constructor-modal/check-icon.svg");
          }
        }
      }

      .constructor-new__form-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        max-width: 100%;
        min-height: 77px;
        padding: 10px 15px;
        font-family: inherit;
        font-weight: 700;
        font-size: 22px;
        line-height: 27px;
        text-align: center;
        letter-spacing: 0.01em;
        text-transform: uppercase;

        @media (max-width: 575px) {
          min-height: 62px;
          font-size: 18px;
          line-height: 21px;
        }
      }

      .constructor-new__info {
        max-width: 330px;
        margin: 20px auto 7px;
        font-family: inherit;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        color: #4f4f4f;
        opacity: 0.9;

        @media (max-width: 575px) {
          margin: 20px auto 0;
        }

        a {
          color: #007aeb;
          transition: 0.3s all;

          &:hover {
            color: #ff0000;
          }
        }
      }
    }
  }
}
// анимация для картинок допов
@keyframes dopsImgShow {
  0% {
    opacity: 0;
    transform: scale(0);
    will-change: transform, filter;
  }

  70% {
    opacity: 1;
    will-change: transform, filter;
  }

  80% {
    transform: scale(1.2);
    filter: drop-shadow(0 0 6px rgba(0, 0, 0, 0.3));
    will-change: transform, filter;
  }

  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 0 rgba(0, 0, 0, 0.3));
    will-change: auto;
  }
}
