
@import "../../scss/variables";
#working-mode {
  padding: 152px 0 46px;
  overflow: hidden;

  @media (max-width: 991px) {
    padding: 100px 0 46px;
  }

  @media (max-width: 767px) {
    padding: 41px 0 25px;
  }

  .title-box {
    width: 100%;
    margin-bottom: 45px;

    @media (max-width: 575px) {
      margin-bottom: 21px;
    }

    &__subtitle {
      @media (max-width: 575px) {
        margin-top: 8px;
      }
    }
  }

  .mods {
    font-family: "Inter", sans-serif;
    width: 100%;

    .mods__togglers {
      position: relative;
      width: 100%;

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 101vw;
        height: 1px;
        background-color: #333333;
      }

      .swiper {
        width: 100%;
      }

      .swiper-wrapper {
        justify-content: space-between;
      }

      .swiper-slide {
        height: auto;
        width: 222px !important;
        margin-right: 6px;
        cursor: pointer;
        border: 1px #e0e0e0 solid;
        border-bottom: 1px transparent solid;
        border-radius: 10px 10px 0px 0px;
        transition: 0.3s border-color;

        @media (max-width: 575px) {
          width: 160px !important;
        }

        .swiper-slide-content {
          height: 100%;
          padding: 20px 12px 12px;
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          text-align: center;
          color: #828282;
          transition: 0.3s all;

          @media (max-width: 575px) {
            padding: 15px 8px 8px;
            font-weight: 700;
            font-size: 16px;
            line-height: 20px;
          }
        }

        &:hover {
          border: 1px #333333 solid;
          border-bottom: 1px transparent solid;
        }

        &_active {
          border: 1px #333333 solid;

          .swiper-slide-content {
            position: relative;
            color: #333333;
            font-weight: 700;

            &::before {
              content: "";
              position: absolute;
              bottom: -2px;
              left: 0;
              width: 100%;
              height: 4px;
              background: #ffffff;
            }
          }
        }
      }
    }

    .fade-enter-active,
    .fade-leave-active {
      transition: opacity 0.35s ease;
    }

    .fade-enter-from,
    .fade-leave-to {
      position: absolute;
      width: 100%;
      top: 0;
      opacity: 0;
    }

    .mods__cards {
      position: relative;
      width: 100%;
      margin-top: 86px;

      @media (max-width: 767px) {
        margin-top: 26px;
      }

      .card {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        column-gap: 32px;
        width: 100%;

        @media (max-width: 767px) {
          flex-direction: column-reverse;
        }
      }

      .card-text-box {
        max-width: 509px;
        width: 100%;

        @media (max-width: 767px) {
          max-width: 100%;
          margin-top: 20px;
        }

        .card__title {
          margin-bottom: 5px;
          font-weight: 700;
          font-size: 32px;
          line-height: 39px;
          color: $text-black-gray;

          @media (max-width: 575px) {
            display: flex;
            column-gap: 10px;
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
            margin-bottom: 10px;
          }
        }
        .card__subtitle {
          margin-bottom: 12px;
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          letter-spacing: 0.01em;
          color: $text-black-gray;

          @media (max-width: 575px) {
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
          }
        }

        .card__text {
          margin-bottom: 0;
          font-weight: 400;
          font-size: 20px;
          line-height: 28px;
          color: $text-black-gray;

          @media (max-width: 575px) {
            font-weight: 400;
            font-size: 16px;
            line-height: 22px;
          }
        }

        .recipe-title {
          margin-top: 12px;
          font-weight: 600;
          font-size: 20px;
          line-height: 28px;
          color: $text-black-gray;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 22px;
          }
        }

        .recipe-list {
          padding: 12px 0 0 16px;

          li {
            position: relative;
            font-weight: 400;
            font-size: 20px;
            line-height: 28px;
            color: $text-black-gray;

            @media (max-width: 575px) {
              font-weight: 400;
              font-size: 16px;
              line-height: 22px;
            }

            &:before {
              content: "-";
              position: absolute;
              left: -15px;
              top: 0;
              color: currentColor;
            }
          }
        }

        .features-list {
          display: flex;
          flex-direction: column;
          align-items: stretch;
          row-gap: 34px;
          margin-top: 36px;

          @media (max-width: 575px) {
            margin-top: 20px;
            row-gap: 16px;
          }

          li {
            display: flex;
            align-items: flex-start;
            width: 100%;
          }

          img {
            flex-shrink: 0;
            width: 24px;
            height: auto;
            margin-right: 12px;
            padding-top: 3px;
          }

          &__text-box {
            flex-grow: 1;
          }

          &__title {
            font-weight: 400;
            font-size: 20px;
            line-height: 28px;
            color: $text-black-gray;

            @media (max-width: 575px) {
              font-weight: 400;
              font-size: 16px;
              line-height: 22px;
            }

            span {
              font-weight: 700;
              color: $btnred;

              @media (max-width: 575px) {
                font-weight: 400;
              }
            }
          }
        }
      }

      .card-imgs-box {
        max-width: 1050px;
        width: 100%;

        @media (max-width: 1299px) {
          max-width: calc(100% - 509px - 32px);
        }

        @media (max-width: 1099px) {
          position: relative;
          max-width: 330px;
          margin: 0 auto;
        }

        .card-imgs-swiper {
          width: 100%;
          overflow: hidden;
        }

        .swiper-wrapper {
          display: flex;
          column-gap: 32px;
          width: 100%;

          @media (max-width: 1300px) {
            column-gap: 0;
          }
        }

        .swiper-slide {
          display: flex;
          width: 1px;
          flex-grow: 1;

          picture {
            display: flex;
          }

          img {
            width: 100%;
            max-width: 100%;
          }
        }

        .imgs-pagination {
          display: flex;
          justify-content: center;
          margin-top: 10px;

          @media (max-width: 1099px) {
            position: absolute;
            bottom: 17px;
            width: 100%;
            z-index: 2;
          }

          @media (min-width: 1300px) {
            display: none;
          }

          span {
            width: 20px;
            height: 20px;
            margin: 0 6px;
            background-color: #bdbdbd;
            opacity: 1;

            transition: 0.3s all;
            &:hover {
              background-color: $primary-color;
            }

            &.swiper-pagination-bullet-active {
              background-color: $primary-color;
            }
          }
        }

        .imgs-navigation {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          z-index: 2;
          width: 100%;
          padding: 0 13px;
          display: flex;
          justify-content: space-between;
          pointer-events: none;
          @media (min-width: 1100px) {
            display: none;
          }

          .imgs-btn {
            width: 28px;
            height: 28px;
            background-position: center;
            background-size: 100% 100%;
            cursor: pointer;
            pointer-events: all;
            transition: 0.3s all;

            &:hover:not(.swiper-button-disabled) {
              transform: scale(1.07);
            }
          }

          .imgs-btn_next {
            background-image: url("../images/global/slider-next.svg");
          }
          .imgs-btn_prev {
            background-image: url("../images/global/slider-prev.svg");
          }

          .swiper-button-disabled {
            filter: grayscale(1);
          }
        }
      }
    }
  }
}
