#kit {
  padding: 150px 0 75px;
  background: #ffffff;
  border-bottom: 1px solid #bdbdbd;
  border-top: 1px solid #bdbdbd;
}
@media (max-width: 991px) {
  #kit {
    padding: 75px 0 75px;
  }
}
@media (max-width: 767px) {
  #kit {
    padding: 60px 0 60px;
  }
}
@media (max-width: 575px) {
  #kit {
    border: none;
    padding: 30px 0 0;
  }
}
#kit img:not([src]):not([srcset]) {
  visibility: hidden;
}
#kit .title-box {
  margin-bottom: 3px;
}
@media (max-width: 575px) {
  #kit .title-box {
    margin-bottom: 10px;
  }
}
#kit .fade-enter-active,
#kit .fade-leave-active {
  transition: opacity 0.4s ease;
}
#kit .fade-enter-from,
#kit .fade-leave-to {
  opacity: 0 !important;
}
#kit .kit {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  font-family: "Inter", sans-serif;
}
@media (max-width: 1399px) {
  #kit .kit {
    flex-wrap: wrap;
  }
}
#kit .kit__dop-wrap {
  max-width: 545px;
  width: 100%;
  padding-top: 85px;
}
@media (max-width: 1199px) {
  #kit .kit__dop-wrap {
    padding-top: 55px;
    max-width: 48%;
  }
}
@media (max-width: 991px) {
  #kit .kit__dop-wrap {
    max-width: 52%;
  }
}
@media (max-width: 767px) {
  #kit .kit__dop-wrap {
    max-width: 100%;
  }
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap {
    padding-top: 0;
  }
}
#kit .kit__dop-wrap .dops-item {
  display: flex;
  align-items: center;
  column-gap: 15px;
  padding: 12px 0;
  width: 100%;
}
#kit .kit__dop-wrap .dops-item:not(:first-child) {
  padding-top: 27px;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap .dops-item {
    column-gap: 0;
    padding: 4px 0 7px;
  }
}
#kit .kit__dop-wrap .dops-item__icon {
  max-width: 68px;
}
#kit .kit__dop-wrap .dops-item__icon img {
  max-width: 100%;
}
#kit .kit__dop-wrap .dops-item__text-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 15px;
  flex-grow: 1;
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap .dops-item__text-wrap {
    flex-grow: 0;
    flex-direction: column;
    align-items: stretch;
    max-width: 170px;
    width: 100%;
    margin-left: auto;
  }
}
#kit .kit__dop-wrap .dops-item__name {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  text-decoration-line: underline;
  color: #323232;
  transition: 0.3s all;
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap .dops-item__name {
    font-size: 16px;
    line-height: 19px;
  }
}
#kit .kit__dop-wrap .dops-item__name:hover {
  color: #ff0000;
}
#kit .kit__dop-wrap .dops-item__price-wrap {
  margin-left: auto;
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap .dops-item__price-wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    column-gap: 10px;
    margin-left: 0;
  }
}
#kit .kit__dop-wrap .dops-item__price {
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  text-align: right;
  color: #323232;
  white-space: nowrap;
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap .dops-item__price {
    font-size: 16px;
    line-height: 28px;
  }
}
#kit .kit__dop-wrap .dops-item__oldprice {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  text-decoration-line: line-through;
  color: #323232;
  white-space: nowrap;
}
@media (max-width: 575px) {
  #kit .kit__dop-wrap .dops-item__oldprice {
    font-size: 16px;
    line-height: 19px;
  }
}
#kit .kit__img-wrap {
  max-width: 570px;
  width: 100%;
  aspect-ratio: 57/84;
  margin-left: auto;
}
@media (max-width: 1399px) {
  #kit .kit__img-wrap {
    position: sticky;
    top: 110px;
    max-width: 520px;
    margin-right: auto;
  }
}
@media (max-width: 1199px) {
  #kit .kit__img-wrap {
    max-width: 44%;
  }
}
@media (max-width: 991px) {
  #kit .kit__img-wrap {
    max-width: 47%;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  #kit .kit__img-wrap {
    position: static;
    max-width: 450px;
    margin-right: auto;
  }
}
#kit .kit__img-wrap .img-box {
  position: relative;
  width: 100%;
  height: 100%;
}
@media (max-width: 1199px) and (min-width: 992px) {
  #kit .kit__img-wrap .img-box {
    max-height: 85vh;
  }
}
#kit .kit__img-wrap .img-box__main {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  height: 100%;
}
#kit .kit__img-wrap .img-box__main-product {
  position: absolute;
  left: 11.579%;
  top: 0;
  grid-row: 1/2;
  grid-column: 1/2;
  width: 59.122%;
  height: 81.428%;
}
@media (max-width: 1199px) and (min-width: 992px) {
  #kit .kit__img-wrap .img-box__main-product {
    display: flex;
    justify-content: center;
  }
}
#kit .kit__img-wrap .img-box__main-product img {
  width: 100%;
}
#kit .kit__img-wrap .img-box .img-dop {
  position: absolute;
  opacity: 0.4;
  transition: 0.3s all;
}
#kit .kit__img-wrap .img-box .img-dop_show {
  opacity: 1;
}
#kit .kit__img-wrap .img-box .img-dop img {
  position: static;
  max-width: 100%;
  height: auto;
}
#kit .kit__img-wrap .img-box .img-dop.arom-dop {
  width: 27%;
  bottom: 2.111%;
  right: 25.47%;
}
#kit .kit__img-wrap .img-box .img-dop.dioptr-dop {
  width: 22.375%;
  bottom: 10%;
  right: 21.36%;
}
#kit .kit__img-wrap .img-box .img-dop.kolonna-dop {
  width: 20.683%;
  right: 21.4188%;
  bottom: 28.666%;
}
#kit .kit__img-wrap .img-box .img-dop.popugai-dop {
  width: 28.376%;
  right: -0.6666%;
  bottom: 2.7777%;
}
#kit .kit__img-wrap .img-box .img-dop.spn-m-dop {
  width: 39.83%;
  left: 3.157%;
  bottom: 7.666%;
}
#kit .kit__img-wrap .img-box .img-dop.spn-n-dop {
  width: 46.8376%;
  bottom: 0.6666%;
  left: 9.12%;
}
#kit .kit__img-wrap .img-box .img-dop.zarga-dop {
  width: 19.145%;
  bottom: 21.8888%;
  left: 0;
}
#kit .kit__form-wrap {
  max-width: 400px;
  width: 100%;
  padding-top: 64px;
}
@media (max-width: 1399px) {
  #kit .kit__form-wrap {
    padding-top: 0;
    max-width: 545px;
    margin-top: 20px;
  }
}
@media (max-width: 1199px) {
  #kit .kit__form-wrap {
    max-width: 48%;
  }
}
@media (max-width: 991px) {
  #kit .kit__form-wrap {
    max-width: 52%;
  }
}
@media (max-width: 767px) {
  #kit .kit__form-wrap {
    max-width: 100%;
  }
}
#kit .kit__form-wrap .form-box {
  width: 100%;
  padding: 24px 28px 34px;
  background: #f3f3f3;
  border-radius: 10px;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box {
    width: calc(100% + 40px);
    padding: 24px 20px 24px;
    margin: 0 -20px;
    border-radius: 0;
  }
}
#kit .kit__form-wrap .form-box .inputs-box {
  row-gap: 20px;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box .inputs-box {
    row-gap: 15px;
  }
}
#kit .kit__form-wrap .form-box__radio-btns {
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  width: 100%;
  margin-bottom: 14px;
}
#kit .kit__form-wrap .form-box__radio-label {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 20px;
  padding: 6px 0 10px;
  cursor: pointer;
}
#kit .kit__form-wrap .form-box__radio-label:not(:last-child) {
  border-bottom: 1px solid #e0e0e0;
}
#kit .kit__form-wrap .form-box__radio-label div {
  position: relative;
  width: 32px;
  height: 32px;
  border: 2px solid #323232;
  border-radius: 50%;
  background: transparent;
  transition: 0.3s all;
}
#kit .kit__form-wrap .form-box__radio-label:hover div {
  border-color: #ff0000;
  box-shadow: 0 0 4px 1px rgba(255, 0, 0, 0.3);
}
#kit .kit__form-wrap .form-box__radio-label input {
  position: absolute;
  visibility: hidden;
  width: 1px;
  height: 0;
  opacity: 0;
  z-index: -1;
}
#kit .kit__form-wrap .form-box__radio-label input:checked + div:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #f82c2c;
}
#kit .kit__form-wrap .form-box__radio-name {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box__radio-name {
    font-size: 16px;
    line-height: 22px;
  }
}
#kit .kit__form-wrap .form-box__radio-price {
  margin-left: auto;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
  text-align: right;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box__radio-price {
    font-size: 16px;
    line-height: 22px;
  }
}
#kit .kit__form-wrap .form-box__radio-price_old {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  text-decoration-line: line-through;
}
#kit .kit__form-wrap .form-box__price-info-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 20px;
  margin: 14px 0 20px;
}
#kit .kit__form-wrap .form-box__price-text {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box__price-text {
    font-size: 18px;
    line-height: 22px;
  }
}
#kit .kit__form-wrap .form-box__price-number {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: right;
  color: #f82c2c;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box__price-number {
    font-size: 18px;
    line-height: 22px;
  }
}
#kit .kit__form-wrap .form-box__form-btn {
  width: 100%;
  min-height: 77px;
  margin-top: 32px;
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box__form-btn {
    min-height: 62px;
    margin-top: 27px;
    border-radius: 10px;
  }
}
#kit .kit__form-wrap .form-box__rassrochka-check {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 10px;
  margin-top: 24px;
}
#kit .kit__form-wrap .form-box__rassrochka-check div {
  width: 24px;
  height: 24px;
  background-size: 16px 16px;
}
#kit .kit__form-wrap .form-box__rassrochka-check span {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #kit .kit__form-wrap .form-box__rassrochka-check span {
    font-size: 20px;
    line-height: 24px;
  }
}
#kit .kit__form-wrap .form-box__rassrochka-check:hover div {
  border-color: #f82c2c;
  box-shadow: 0 0 4px 1px rgba(192, 33, 33, 0.32);
}
#kit .kit__form-wrap .form-box__info {
  margin-top: 20px;
  opacity: 0.9;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #4f4f4f;
}
#kit .kit__form-wrap .form-box__info a {
  color: #007aeb;
  transition: 0.3s all;
}
#kit .kit__form-wrap .form-box__info a:hover {
  color: #ff0000;
}