#recipe-modal {
  display: none;
  max-width: 1050px;
  width: 100%;
  padding: 0;
  border-radius: 10px;
  font-family: "Inter", sans-serif;
}
#recipe-modal .wrapper {
  padding: 32px 38px;
}
@media (max-width: 575px) {
  #recipe-modal .wrapper {
    padding: 20px 20px 24px;
  }
}
#recipe-modal .title {
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #recipe-modal .title {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.01em;
  }
}
#recipe-modal .title br {
  display: none;
}
#recipe-modal .subtitle {
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #828282;
}
#recipe-modal .list-line {
  display: flex;
  flex-direction: column;
  row-gap: 6px;
  margin-bottom: 24px;
}
#recipe-modal .list-line li {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #recipe-modal .list-line li {
    font-size: 16px;
    line-height: 22px;
  }
}
#recipe-modal .comment {
  padding-top: 8px;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #recipe-modal .comment {
    font-size: 16px;
    line-height: 22px;
  }
}
#recipe-modal .list-step {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  padding-top: 8px;
}
#recipe-modal .list-step li {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #recipe-modal .list-step li {
    font-size: 16px;
    line-height: 22px;
  }
}
#recipe-modal .list-step li span {
  font-weight: 700;
  color: #007aeb;
}
@media (max-width: 575px) {
  #recipe-modal .list-step li span {
    font-weight: 400;
  }
}
#recipe-modal .list-number {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  padding-top: 8px;
}
#recipe-modal .list-number li {
  display: flex;
  align-items: flex-start;
  column-gap: 7px;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #recipe-modal .list-number li {
    font-size: 16px;
    line-height: 22px;
  }
}
#recipe-modal .recommendation {
  margin-top: 24px;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #recipe-modal .recommendation {
    font-size: 16px;
    line-height: 22px;
  }
}
#recipe-modal .recommendation span {
  font-weight: 700;
}
#recipe-modal .price {
  margin-top: 24px;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #f82c2c;
}
@media (max-width: 575px) {
  #recipe-modal .price {
    font-size: 16px;
    line-height: 22px;
  }
}