#present-modal {
  display: none;
  max-width: 92vw;
  width: 394px;
  min-height: 160px;
  padding: 22px 22px 22px 22px;
  background: #ffffff;
  border-radius: 10px;
}
#present-modal .present-modal__wrap {
  width: 100%;
  min-height: 100px;
}
#present-modal .present-modal__title {
  margin: 0 0 10px;
  padding-right: 12px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: -0.01em;
  color: #333333;
}
@media (max-width: 575px) {
  #present-modal .present-modal__title {
    padding-right: 15px;
  }
}
#present-modal .present-modal__text ul {
  list-style: none;
  margin: 0;
  padding: 0 0 0 13px;
}
#present-modal .present-modal__text ul li {
  position: relative;
  margin: 0;
}
#present-modal .present-modal__text ul li:before {
  content: "";
  position: absolute;
  left: -12px;
  top: 10px;
  width: 6px;
  height: 1px;
  background-color: currentColor;
}
#present-modal .present-modal__text p,
#present-modal .present-modal__text li {
  font-weight: 400;
  font-size: 17px;
  line-height: 22px;
  letter-spacing: -0.01em;
  color: #828282;
}