#gallery {
  padding: 153px 0 129px;
  background: #f3f3f3;
}
@media (max-width: 991px) {
  #gallery {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  #gallery {
    padding: 70px 0;
  }
}
@media (max-width: 575px) {
  #gallery {
    padding: 24px 0 40px;
  }
}
@media (max-width: 575px) {
  #gallery .content-container {
    padding: 0;
  }
}
#gallery .title-box {
  width: 100%;
  margin-bottom: 60px;
}
@media (max-width: 575px) {
  #gallery .title-box {
    padding: 0 20px;
    margin-bottom: 22px;
  }
}
#gallery .title-box__subtitle {
  font-family: "Inter", sans-serif;
}
@media (max-width: 575px) {
  #gallery .title-box__subtitle {
    line-height: 20px;
  }
}
#gallery .grid-box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 576px) {
  #gallery .grid-box {
    column-gap: 27px;
    row-gap: 20px;
  }
}
@media (max-width: 1199px) {
  #gallery .grid-box {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media (max-width: 767px) {
  #gallery .grid-box {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
}
@media (max-width: 575px) {
  #gallery .grid-box {
    display: flex;
  }
}
#gallery .grid-box .img-wrap-1 {
  grid-column: 1/2;
  grid-row: 1/3;
  display: flex;
}
#gallery .grid-box .img-wrap {
  transition: 0.4s all;
  border-radius: 10px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0);
  cursor: zoom-in;
}
#gallery .grid-box .img-wrap a {
  display: flex;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  cursor: zoom-in;
  overflow: hidden;
}
#gallery .grid-box .img-wrap a picture {
  display: flex;
  width: 100%;
}
#gallery .grid-box .img-wrap a img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.6s cubic-bezier(0.2, 1.03, 0.55, 0.7) transform;
}
@media (min-width: 992px) {
  #gallery .grid-box .img-wrap:hover {
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.2);
  }
  #gallery .grid-box .img-wrap:hover img {
    transform: scale(1.15);
  }
}
#gallery .gallery-swiper {
  position: relative;
  width: 100%;
}
@media (max-width: 575px) {
  #gallery .gallery-swiper {
    max-width: 400px;
    overflow: hidden;
    margin: 0 auto;
  }
}
#gallery .gallery-swiper .gallery-pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  width: 100%;
  z-index: 2;
}
@media (min-width: 576px) {
  #gallery .gallery-swiper .gallery-pagination {
    display: none;
  }
}
#gallery .gallery-swiper .gallery-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
}
#gallery .gallery-swiper .gallery-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#gallery .gallery-swiper .gallery-navigation {
  position: absolute;
  top: calc(50% - 20px);
  transform: translateY(-50%);
  z-index: 2;
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 576px) {
  #gallery .gallery-swiper .gallery-navigation {
    display: none;
  }
}
#gallery .gallery-swiper .gallery-navigation .gallery-btn {
  position: relative;
  width: 50px;
  height: 100px;
  background-color: #ffffff;
  background-position: 7px center;
  background-size: 28px 28px;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#gallery .gallery-swiper .gallery-navigation .gallery-btn_next {
  background-image: url("../images/global/slider-next.svg");
  background-position: 14px center;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
}
#gallery .gallery-swiper .gallery-navigation .gallery-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}
#gallery .gallery-swiper .gallery-navigation .swiper-button-disabled {
  filter: grayscale(1);
}