
@import "../../scss/variables";
#braga {
  padding: 150px 0 145px;
  background: $section-gray-bg;
  overflow: hidden;

  @media (max-width: 991px) {
    padding: 100px 0;
  }

  @media (max-width: 767px) {
    padding: 70px 0;
  }

  @media (max-width: 575px) {
    padding: 30px 0 32px;
  }

  .title-box {
    margin-bottom: 81px;

    @media (max-width: 991px) {
      margin-bottom: 66px;
    }

    @media (max-width: 575px) {
      margin-bottom: 35px;
    }
  }

  .content-container {
    &_special {
      max-width: 1860px;
      margin: 0 auto;

      @media (max-width: 1879px) {
        max-width: 1250px;
      }

      @media (max-width: 1199px) {
        max-width: 700px;
      }

      @media (max-width: 991px) {
        max-width: 580px;
      }
    }
  }

  .swiper-box {
    width: 100%;
    padding: 0 100px;
    overflow: hidden;

    @media (max-width: 1199px) {
      position: relative;
      padding: 0 30px;
    }

    @media (max-width: 991px) {
      padding: 0 50px;
    }

    @media (max-width: 575px) {
      padding: 0;
    }

    .braga-swiper {
      position: relative;

      @media (max-width: 1199px) {
        position: static;
        overflow: hidden;
      }
    }
    .swiper-wrapper {
      @media (min-width: 1880px) {
        flex-direction: row-reverse;
        transform: translate3d(13.45%, 0px, 0px) !important;
      }
    }

    .slide.swiper-slide-next + .slide {
      transition: 0.3s transform;

      @media (min-width: 1880px) {
        order: 3;
        opacity: 1;
        width: max-content !important;
        transform: scale(0.86) translateX(-50%);
        .slide__wrap {
          transform: translateX(100%);
          background: rgba(224, 224, 224);
        }
        .slide__img-wrap {
          img {
            opacity: 0.7;
          }
        }
        .slide__name-absolute {
          right: auto;
          left: 24px;
          text-align: left;
        }
      }
      @media (max-width: 991px) {
        opacity: 0;
        visibility: hidden;
      }
    }

    .slide {
      max-width: 63.253%;
      width: 100% !important;
      font-family: "Inter", sans-serif;
      opacity: 0;
      @media (min-width: 1880px) {
        transform: scale(0.1);
        transition: 0.6s opacity, 0.6s transform;
      }
      @media (max-width: 1879px) {
        max-width: 100%;
        width: 100% !important;
      }

      &.swiper-slide-active {
        position: relative;
        z-index: 10;
        opacity: 1;
        transition: 0.3s all;

        @media (min-width: 1880px) {
          transform: scale(1);
          order: 2;
          transition: 0.6s opacity, 0.6s transform;
        }

        .slide__name-absolute {
          display: none;

          @media (max-width: 1199px) {
            display: flex;
          }
        }

        .slide__price-wrap {
          display: none;
        }

        .slide__text-wrap {
          height: auto;
          display: block;
        }
        @media (min-width: 1880px) {
          .slide__img-wrap {
            width: 47%;
          }

          .slide__text-wrap {
            width: calc(100% - 47% - 34px);
            max-width: 100%;
          }
        }
      }

      &.swiper-slide-prev {
        transition: 0.3s transform;

        @media (min-width: 1880px) {
          order: 3;
          opacity: 1;
          width: max-content !important;
          transform: scale(0.86) translateX(-50%);
          .slide__wrap {
            background: rgba(224, 224, 224);
            transform: translateX(100%);
          }
          .slide__img-wrap {
            img {
              opacity: 0.7;
            }
          }
          .slide__name-absolute {
            right: auto;
            left: 24px;
            text-align: left;
          }
        }
        @media (max-width: 991px) {
          opacity: 0;
          visibility: hidden;
        }
      }

      &.swiper-slide-next {
        transition: 0.3s transform;

        @media (min-width: 1880px) {
          order: 1;
          opacity: 1;
          width: max-content !important;
          transform: scale(0.86) translateX(50%);
          .slide__wrap {
            // flex-direction: row-reverse;
            background: rgba(224, 224, 224);
            transform: translateX(-100%);
          }
          .slide__img-wrap {
            img {
              opacity: 0.7;
            }
          }
          .slide__name-absolute {
            right: 24px;
            left: auto;
            text-align: right;
          }
        }

        @media (max-width: 1199px) {
          opacity: 1;
        }

        @media (max-width: 991px) {
          opacity: 0;
          visibility: hidden;
        }
      }

      &__wrap {
        display: flex;
        align-items: flex-start;
        column-gap: 34px;
        width: 100%;
        height: 100%;
        padding: 40px 25px 38px 40px;
        border-radius: 10px;
        background: #ffffff;
        @media (max-width: 1879px) {
          transition: 0.3s all;
        }

        @media (max-width: 1199px) {
          padding: 16px;
        }

        @media (max-width: 575px) {
          padding: 0;
          border-radius: 0;
        }
      }

      &__img-wrap {
        position: relative;
        width: 47%;
        overflow: hidden;

        @media (min-width: 1880px) {
          width: 100%;
          @media (min-width: 1880px) {
            width: 100%;
          }
        }

        @media (max-width: 1199px) {
          display: flex;
          flex-direction: column;
          align-items: stretch;
          width: 100%;
          // background: rgba(51, 51, 51, 0.8);
        }

        img {
          width: 100%;
          transition: 0.3s all;

          @media (max-width: 575px) {
            object-fit: cover;
            max-height: 600px;
          }
        }
      }

      &__name-absolute {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 60%;
        z-index: 2;

        @media (max-width: 1199px) {
          top: 0;
          left: 0;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          width: 100%;
          height: 100%;
          transform: translateY(0);
          padding: 20px;
          background: linear-gradient(
            360deg,
            #000000 0%,
            rgba(0, 0, 0, 0) 55.85%
          );
        }
      }

      &__name {
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        color: #ffffff;

        @media (max-width: 1199px) {
          text-align: center;
        }

        @media (max-width: 575px) {
          font-size: 20px;
          line-height: 24px;
          letter-spacing: 0.01em;

          br {
            display: none;
          }
        }
      }

      &__btn {
        display: none;
        max-width: 165px;
        width: 100%;
        min-height: 43px;
        border-radius: 10px;
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        letter-spacing: 0.01em;

        @media (max-width: 1199px) {
          display: flex;
          margin: 12px auto 0;
        }
      }

      &__price-wrap {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 5px;
        background: rgba(0, 0, 0, 0.5);
        transition: 0.3s all;

        @media (max-width: 1199px) {
          display: none;
        }
      }

      &__text-wrap {
        overflow: hidden;
        width: calc(100% - 47% - 34px);

        @media (min-width: 1880px) {
          height: 0;
          width: 0;
          max-width: 0;
          display: none;
          transition: 0.4s all;
        }

        @media (max-width: 1199px) {
          display: none !important;
        }
      }

      &__title {
        margin-bottom: 8px;
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        color: $text-black-gray;
      }

      &__subtitle {
        margin-bottom: 24px;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 0.01em;
        color: #828282;
      }

      &__list-line {
        display: flex;
        flex-direction: column;
        row-gap: 6px;
        margin-bottom: 24px;

        li {
          font-weight: 400;
          font-size: 20px;
          line-height: 28px;
          color: $text-black-gray;
        }
      }

      &__list-number {
        display: flex;
        flex-direction: column;
        row-gap: 24px;
        padding-top: 8px;

        li {
          display: flex;
          align-items: flex-start;
          column-gap: 7px;
          font-weight: 400;
          font-size: 20px;
          line-height: 28px;
          color: $text-black-gray;
        }
      }

      &__link {
        margin-left: 24px;
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        text-decoration-line: underline;
        overflow: hidden;
        color: $primary-color;
        outline: none;
        transition: 0.3s all;

        @media (min-width: 992px) {
          &:hover {
            color: $btnred;
          }
        }
      }
    }

    .braga-pagination {
      display: flex;
      justify-content: center;
      margin-top: 25px;
      width: 100%;
      z-index: 2;

      @media (max-width: 767px) {
        margin-top: 20px;
      }

      span {
        width: 24px;
        height: 24px;
        margin: 0 12px;
        background-color: #bdbdbd;
        opacity: 1;
        transition: 0.3s all;

        @media (min-width: 768px) {
          &:hover {
            background-color: $primary-color;
          }
        }

        @media (max-width: 767px) {
          width: 20px;
          height: 20px;
          margin: 0 6px;
        }

        &.swiper-pagination-bullet-active {
          background-color: $primary-color;
        }
      }
    }

    .braga-navigation {
      position: absolute;
      top: calc(50% - 25px);
      left: -85px;
      transform: translate(0, -50%);
      z-index: 2;
      width: calc(100% + 170px);
      padding: 0;
      display: flex;
      justify-content: space-between;
      pointer-events: none;

      @media (max-width: 1199px) {
        left: 0;
        width: 100%;
      }

      @media (max-width: 991px) {
        left: 0;
        width: 100%;
      }

      @media (max-width: 767px) {
        top: calc(50% - 22px);
      }

      @media (max-width: 575px) {
        left: 0;
        width: 100%;
      }

      .braga-btn {
        position: relative;
        width: 43px;
        height: 43px;
        background-position: center;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        cursor: pointer;
        pointer-events: all;
        transition: 0.3s all;

        @media (min-width: 768px) {
          &:hover:not(.swiper-button-disabled) {
            transform: scale(1.07);
          }
        }

        @media (max-width: 575px) {
          width: 50px;
          height: 100px;
          background-color: #ffffff;
          background-position: 7px center;
          background-size: 28px 28px;
          background-repeat: no-repeat;
        }
      }

      .braga-btn_next {
        background-image: url("../images/global/slider-next.svg");

        @media (max-width: 575px) {
          background-position: 14px center;
          border-top-left-radius: 50px;
          border-bottom-left-radius: 50px;
        }
      }
      .braga-btn_prev {
        background-image: url("../images/global/slider-prev.svg");

        @media (max-width: 575px) {
          border-top-right-radius: 50px;
          border-bottom-right-radius: 50px;
        }
      }

      .swiper-button-disabled {
        @media (min-width: 1880px) {
          opacity: 0;
          visibility: hidden;
        }

        filter: grayscale(1);
      }
    }
  }
}
