#comparison {
  padding: 150px 0;
  background: #ffffff;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 991px) {
  #comparison {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  #comparison {
    padding: 42px 0 36px;
  }
}
#comparison .row {
  max-width: 1450px;
  width: 100%;
  overflow: hidden;
}
#comparison .title-box {
  width: 100%;
  margin-bottom: 46px;
}
@media (max-width: 575px) {
  #comparison .title-box {
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  #comparison .title-box__title {
    max-width: 320px;
    margin: 0 auto;
    line-height: 24px;
    letter-spacing: 0.01em;
  }
}
#comparison .title-box__title span {
  color: #007aeb;
}
#comparison .comparison-swiper {
  position: relative;
  width: 100%;
}
@media (max-width: 575px) {
  #comparison .comparison-swiper {
    padding: 0 5px;
  }
}
@media (min-width: 576px) {
  #comparison .comparison-swiper .lists-box {
    display: flex;
    justify-content: space-between;
    width: 100%;
    column-gap: 32px;
  }
}
@media (max-width: 1023px) and (min-width: 576px) {
  #comparison .comparison-swiper .lists-box {
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 50px;
  }
}
@media (min-width: 576px) {
  #comparison .comparison-swiper .lists-box__item {
    display: flex;
    align-items: flex-start;
    width: auto;
    max-width: calc(50% - 16px);
  }
}
@media (max-width: 1023px) and (min-width: 576px) {
  #comparison .comparison-swiper .lists-box__item {
    max-width: 100%;
  }
  #comparison .comparison-swiper .lists-box__item:nth-child(2) {
    flex-direction: row-reverse;
  }
}
#comparison .comparison-swiper .lists-box__img-wrap {
  display: flex;
  justify-content: flex-start;
  max-width: 322px;
  width: 322px;
}
@media (max-width: 575px) {
  #comparison .comparison-swiper .lists-box__img-wrap {
    max-width: 100%;
    width: 46.25vw;
    margin: 0 auto;
  }
}
#comparison .comparison-swiper .lists-box__img-wrap img {
  max-width: 100%;
}
@media (max-width: 575px) {
  #comparison .comparison-swiper .lists-box__img-wrap img {
    aspect-ratio: 299/439;
  }
}
#comparison .comparison-swiper .lists-box__content-wrap {
  padding-top: 52px;
}
@media (max-width: 575px) {
  #comparison .comparison-swiper .lists-box__content-wrap {
    padding-top: 5px;
    width: max-content;
    max-width: 100%;
    margin: 0 auto;
  }
}
#comparison .comparison-swiper .lists-box__item-title {
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  color: #007aeb;
}
@media (max-width: 575px) {
  #comparison .comparison-swiper .lists-box__item-title {
    margin-bottom: 13px;
    font-weight: 500;
    font-size: 22px;
    line-height: 27px;
  }
}
#comparison .comparison-swiper .lists-box__item-title_gray {
  color: #4f4f4f;
}
#comparison .comparison-swiper .lists-box__item-list li {
  position: relative;
  margin-bottom: 19px;
  padding-left: 32px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #comparison .comparison-swiper .lists-box__item-list li {
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 20px;
  }
}
#comparison .comparison-swiper .lists-box__item-list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background-image: url("../images/global/check-circle.svg");
  background-position: center;
  background-repeat: no-repeat;
}
#comparison .comparison-swiper .lists-box__item-list_purchased li::before {
  background-image: url("../images/modal/modal-close-btn.svg");
}
#comparison .comparison-swiper .comparison-pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  width: 100%;
  z-index: 2;
}
@media (min-width: 576px) {
  #comparison .comparison-swiper .comparison-pagination {
    display: none;
  }
}
#comparison .comparison-swiper .comparison-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
}
#comparison .comparison-swiper .comparison-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#comparison .comparison-swiper .comparison-navigation {
  position: absolute;
  left: 50%;
  top: 30vw;
  transform: translateX(-50%);
  z-index: 2;
  width: calc(100% - 10px);
  padding: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 576px) {
  #comparison .comparison-swiper .comparison-navigation {
    display: none;
  }
}
#comparison .comparison-swiper .comparison-navigation .comparison-btn {
  position: relative;
  width: 28px;
  height: 28px;
  background-position: center;
  background-size: 28px 28px;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#comparison .comparison-swiper .comparison-navigation .comparison-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#comparison .comparison-swiper .comparison-navigation .comparison-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#comparison .comparison-swiper .comparison-navigation .swiper-button-disabled {
  filter: grayscale(1);
}