/* inter-300 - latin_cyrillic */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Inter';
    font-style: normal;
    font-weight: 300;
    src: url('../../../styles/fonts/inter-v12-latin_cyrillic-300.eot'); /* IE9 Compat Modes */
    src: url('../../../styles/fonts/inter-v12-latin_cyrillic-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../../../styles/fonts/inter-v12-latin_cyrillic-300.woff2') format('woff2'), /* Super Modern Browsers */
         url('../../../styles/fonts/inter-v12-latin_cyrillic-300.woff') format('woff'), /* Modern Browsers */
         url('../../../styles/fonts/inter-v12-latin_cyrillic-300.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../../../styles/fonts/inter-v12-latin_cyrillic-300.svg#Inter') format('svg'); /* Legacy iOS */
  }
