
@import "../../scss/variables";

#feedback {
  padding: 151px 0 73px;
  background: $section-gray-bg;

  @media (max-width: 991px) {
    padding: 100px 0 73px;
  }

  @media (max-width: 767px) {
    padding: 80px 0 50px;
  }

  @media (max-width: 575px) {
    padding: 33px 0 38px;
  }

  .title-box {
    width: 100%;
    margin-bottom: 52px;

    @media (max-width: 575px) {
      margin-bottom: 16px;
    }

    &__title {
      @media (max-width: 575px) {
        letter-spacing: 0.01em;
        text-transform: uppercase;
      }
    }
  }

  .swiper-box {
    position: relative;
    max-width: 1296px;
    width: 100%;
    padding: 0 120px;

    @media (max-width: 1199px) {
      padding: 0 80px;
    }

    @media (max-width: 767px) {
      padding: 0 45px;
      max-width: 420px;
    }

    @media (max-width: 575px) {
      max-width: 350px;
    }

    .feedback-swiper {
      align-items: stretch;
      width: 100%;
      overflow: hidden;

      .swiper-slide {
        height: auto;
        width: calc((100% / 3) - (124px / 3)) !important;

        @media (max-width: 991px) {
          width: calc(50% - 25px) !important;
        }

        @media (max-width: 767px) {
          width: 100% !important;
        }

        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }

      .feedback-pagination {
        display: flex;
        justify-content: center;
        margin-top: 116px;
        width: 100%;
        z-index: 2;

        @media (max-width: 1199px) {
          margin-top: 56px;
        }

        @media (max-width: 767px) {
          margin-top: 20px;
        }

        span {
          width: 24px;
          height: 24px;
          margin: 0 12px;
          background-color: #bdbdbd;
          opacity: 1;
          transition: 0.3s all;
          pointer-events: none;
          // @media (min-width: 768px) {
          //   &:hover {
          //     background-color: $primary-color;
          //   }
          // }

          @media (max-width: 767px) {
            width: 20px;
            height: 20px;
            margin: 0 6px;
          }

          &.swiper-pagination-bullet-active {
            background-color: $primary-color;
          }
        }
      }

      .feedback-navigation {
        position: absolute;
        top: calc(50% - 70px);
        left: 0;
        transform: translate(0, -50%);
        z-index: 2;
        width: 100%;
        padding: 0;
        display: flex;
        justify-content: space-between;
        pointer-events: none;

        @media (max-width: 1199px) {
          top: calc(50% - 40px);
        }

        @media (max-width: 767px) {
          top: calc(50% - 20px);
        }

        .feedback-btn {
          position: relative;
          width: 43px;
          height: 43px;
          background-position: center;
          background-size: 100% 100%;
          background-repeat: no-repeat;
          cursor: pointer;
          pointer-events: all;
          transition: 0.3s all;

          @media (min-width: 768px) {
            &:hover:not(.swiper-button-disabled) {
              transform: scale(1.07);
            }
          }

          @media (max-width: 767px) {
            width: 28px;
            height: 28px;
          }
        }

        .feedback-btn_next {
          background-image: url("../images/global/slider-next.svg");
        }
        .feedback-btn_prev {
          background-image: url("../images/global/slider-prev.svg");
        }

        .swiper-button-disabled {
          filter: grayscale(1);
        }
      }
    }
  }
}
