
@import "../../scss/variables";
#new-header-test-top-v2 {
  background: #323232;
  font-family: "Montserrat", sans-serif;
  position: relative;
  z-index: 10;
  min-height: 95px;
  max-width: 100vw;
  overflow-x: hidden;
  @media (max-width: 991px) {
    position: relative;
    min-height: 66px;
  }

  [v-cloak] {
    display: none;
  }

  .bg-header-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100vw;
    min-height: 95px;
    background: rgb(50, 50, 50);
    display: flex;
    z-index: 100;
    transition: 0.4s box-shadow ease-in-out;

    &_shadow {
      box-shadow: 0 7px 9px 1px rgb(0, 0, 0, 0.3);
    }

    @media (max-width: 991px) {
      min-height: 66px;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #323232;
        z-index: 2;
      }
    }
  }

  .content-container {
    justify-content: center;
  }

  .header-top__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    column-gap: 16px;
    padding: 15px 0;

    @media (max-width: 991px) {
      padding: 10px 0;
      column-gap: 30px;
    }
  }

  .header-top__logo {
    display: flex;
    align-items: center;

    @media (max-width: 1430px) {
      flex-direction: column;
    }

    @media (max-width: 991px) {
      position: relative;
      z-index: 11;
    }

    img {
      width: 115px;
      height: 65px;

      @media (max-width: 1080px) {
        width: 87px;
        height: auto;
      }

      @media (max-width: 991px) {
        width: 62px;
        height: 30px;
      }

      @media (max-width: 767px) {
        width: 100%;
        max-width: 164px;
        height: auto;
      }
    }

    .logo__text-box {
      margin-left: 40px;
      @media (max-width: 1499px) {
        display: none;
      }
    }

    .logo__text {
      width: max-content;
      max-width: 175px;
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      color: #dedede;
    }
  }

  @media (max-width: 991px) and (min-width: 480px) {
    .menu-container span {
      display: block;
    }
  }

  .menu-container {
    @media (min-width: 992px) {
      justify-content: space-between;
      max-width: 600px;
      width: 100%;
    }

    @media (max-width: 991px) {
      position: static;
      width: 30px;
      height: 26px;
      background: transparent;
      order: 3;
      z-index: auto;
    }

    @media (max-width: 575px) {
      width: 22px;
      height: 18px;
    }

    .menu-toggler {
      display: none;

      @media (max-width: 991px) {
        display: block;
      }

      position: relative;
      width: 30px;
      height: 26px;
      background: transparent;
      background-image: none !important;
      cursor: pointer;
      z-index: 11;
      transition: 0.4s all;

      @media (max-width: 575px) {
        width: 22px;
        height: 18px;
      }

      span {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 30px;
        height: 2px;
        border-radius: 25px;
        background-color: #ffffff;
        background-image: none !important;
        transition: 0.4s all;

        @media (max-width: 575px) {
          width: 22px;
        }

        &:nth-child(1) {
          top: 0;
          transition: 0.3s all 0.2s;
        }

        &:nth-child(2) {
          top: 50%;
          transform: translateX(-50%) translateY(-50%);
        }

        &:nth-child(3) {
          top: 50%;
          transform: translateX(-50%) translateY(-50%);
        }

        &:nth-child(4) {
          top: 24px;
          transition: 0.3s all 0.2s;
          @media (max-width: 575px) {
            top: 16px;
          }
        }
      }

      &_open {
        transform: rotate(90deg);
        span {
          &:nth-child(1) {
            opacity: 0;
            transition: 0.1s all;
          }

          &:nth-child(2) {
            width: 33px;
            transform: translateX(-50%) translateY(-50%) rotate(45deg);

            @media (max-width: 575px) {
              width: 24px;
            }
          }

          &:nth-child(3) {
            width: 33px;
            transform: translateX(-50%) translateY(-50%) rotate(-45deg);

            @media (max-width: 575px) {
              width: 24px;
            }
          }

          &:nth-child(4) {
            opacity: 0;
            transition: 0.1s all;
          }
        }
      }
    }

    .header-top__menu {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      row-gap: 10px;
      column-gap: 30px;
      width: auto;
      margin: 0;

      @media (max-width: 1199px) {
        column-gap: 10px;
      }

      @media (min-width: 992px) {
        justify-content: space-between;
        max-width: 600px;
        width: 100%;
      }

      @media (max-width: 991px) {
        top: 100%;
        right: 0;
        flex-direction: column;
        align-items: stretch;
        row-gap: 5px;
        padding: 16px 0 10px;
        width: 100%;
        transform: translateY(-100%);
        border: none;
        background: rgb(50, 50, 50, 97%);
        box-shadow: 0 0 12px 1px rgb(0, 0, 0, 70%);
        opacity: 0;
        z-index: 1;
        transition: 0.4s;
        position: absolute;

        &_visible {
          opacity: 1;
          transform: translateY(-1px);
        }
      }

      li {
        margin: 0px;

        @media (max-width: 991px) {
          margin: 0 5px;
          text-align: center;
        }

        @media (max-width: 991px) {
          margin: 0;
        }
      }

      a {
        padding: 5px 10px;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        color: #ffffff;
        transition: 0.4s all;

        @media (max-width: 1549px) {
          padding: 5px 10px;
        }

        @media (max-width: 991px) {
          font-size: 15px;
          line-height: 18px;
          padding: 5px 5px;
        }

        @media (max-width: 991px) {
          width: 100%;
          padding: 5px 15px;
          font-size: 18px;
          line-height: 22px;
        }

        @media (max-width: 575px) {
          padding: 5px;
          font-size: 15px;
          line-height: 18px;
        }

        &:hover {
          color: $primary-color;
        }

        @media (min-width: 768px) {
          background: linear-gradient(
            to left,
            #ffffff 50%,
            $primary-color 50%
          ) !important;
          -webkit-background-clip: text !important;
          -webkit-text-fill-color: transparent !important;
          background-size: 200% 100% !important;
          background-position: right !important;

          &:hover {
            background: linear-gradient(
              to left,
              #ffffff 50%,
              $primary-color 50%
            ) !important;
            -webkit-background-clip: text !important;
            -webkit-text-fill-color: transparent !important;
            background-size: 200% 100% !important;
            background-position: left !important;
          }
        }
      }
    }
  }

  .header-top__phone {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 30px;
    row-gap: 8px;

    @media (max-width: 1379px) {
      flex-direction: column;
    }

    @media (max-width: 991px) {
      position: relative;
      z-index: 11;
      margin-left: auto;
    }

    .header-top__phone-number {
      font-weight: 400;
      font-size: 24px;
      line-height: 29px;
      color: #f0f0f0;
      transition: 0.4s all;

      &:hover {
        color: $primary-color;
      }

      @media (max-width: 1379px) {
        display: none;
      }
    }

    .header-top__phone-button {
      display: flex;
      align-items: center;
      padding: 8px 20px;
      border: 2px solid #33a849;
      border-radius: 22px;
      font-weight: 400;
      font-size: 15px;
      line-height: 18px;
      letter-spacing: -0.001em;
      color: #33a849;
      transition: 0.3s all;
      width: auto;
      height: auto;
      margin-top: 0;

      @media (max-width: 991px) {
        padding: 4px 10px;
      }

      @media (max-width: 575px) {
        font-size: 10px;
        line-height: 12px;
        width: min-content;
        border: none;
      }

      svg {
        width: 26px;
        height: 26px;
        margin-right: 10px;
        transition: 0.3s all;
        flex-shrink: 0;

        path {
          fill: #33a849;
          transition: 0.3s all;
        }
      }

      &:hover {
        color: $primary-color;
        border-color: $primary-color;

        svg {
          transform: rotate(45deg);

          path {
            fill: $primary-color;
          }
        }
      }
    }
  }
}
