#feedback {
  padding: 151px 0 73px;
  background: #f3f3f3;
}
@media (max-width: 991px) {
  #feedback {
    padding: 100px 0 73px;
  }
}
@media (max-width: 767px) {
  #feedback {
    padding: 80px 0 50px;
  }
}
@media (max-width: 575px) {
  #feedback {
    padding: 33px 0 38px;
  }
}
#feedback .title-box {
  width: 100%;
  margin-bottom: 52px;
}
@media (max-width: 575px) {
  #feedback .title-box {
    margin-bottom: 16px;
  }
}
@media (max-width: 575px) {
  #feedback .title-box__title {
    letter-spacing: 0.01em;
    text-transform: uppercase;
  }
}
#feedback .swiper-box {
  position: relative;
  max-width: 1296px;
  width: 100%;
  padding: 0 120px;
}
@media (max-width: 1199px) {
  #feedback .swiper-box {
    padding: 0 80px;
  }
}
@media (max-width: 767px) {
  #feedback .swiper-box {
    padding: 0 45px;
    max-width: 420px;
  }
}
@media (max-width: 575px) {
  #feedback .swiper-box {
    max-width: 350px;
  }
}
#feedback .swiper-box .feedback-swiper {
  align-items: stretch;
  width: 100%;
  overflow: hidden;
}
#feedback .swiper-box .feedback-swiper .swiper-slide {
  height: auto;
  width: calc(33.3333333333% - 41.3333333333px) !important;
}
@media (max-width: 991px) {
  #feedback .swiper-box .feedback-swiper .swiper-slide {
    width: calc(50% - 25px) !important;
  }
}
@media (max-width: 767px) {
  #feedback .swiper-box .feedback-swiper .swiper-slide {
    width: 100% !important;
  }
}
#feedback .swiper-box .feedback-swiper .swiper-slide img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
#feedback .swiper-box .feedback-swiper .feedback-pagination {
  display: flex;
  justify-content: center;
  margin-top: 116px;
  width: 100%;
  z-index: 2;
}
@media (max-width: 1199px) {
  #feedback .swiper-box .feedback-swiper .feedback-pagination {
    margin-top: 56px;
  }
}
@media (max-width: 767px) {
  #feedback .swiper-box .feedback-swiper .feedback-pagination {
    margin-top: 20px;
  }
}
#feedback .swiper-box .feedback-swiper .feedback-pagination span {
  width: 24px;
  height: 24px;
  margin: 0 12px;
  background-color: #bdbdbd;
  opacity: 1;
  transition: 0.3s all;
  pointer-events: none;
}
@media (max-width: 767px) {
  #feedback .swiper-box .feedback-swiper .feedback-pagination span {
    width: 20px;
    height: 20px;
    margin: 0 6px;
  }
}
#feedback .swiper-box .feedback-swiper .feedback-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#feedback .swiper-box .feedback-swiper .feedback-navigation {
  position: absolute;
  top: calc(50% - 70px);
  left: 0;
  transform: translate(0, -50%);
  z-index: 2;
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (max-width: 1199px) {
  #feedback .swiper-box .feedback-swiper .feedback-navigation {
    top: calc(50% - 40px);
  }
}
@media (max-width: 767px) {
  #feedback .swiper-box .feedback-swiper .feedback-navigation {
    top: calc(50% - 20px);
  }
}
#feedback .swiper-box .feedback-swiper .feedback-navigation .feedback-btn {
  position: relative;
  width: 43px;
  height: 43px;
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
@media (min-width: 768px) {
  #feedback .swiper-box .feedback-swiper .feedback-navigation .feedback-btn:hover:not(.swiper-button-disabled) {
    transform: scale(1.07);
  }
}
@media (max-width: 767px) {
  #feedback .swiper-box .feedback-swiper .feedback-navigation .feedback-btn {
    width: 28px;
    height: 28px;
  }
}
#feedback .swiper-box .feedback-swiper .feedback-navigation .feedback-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#feedback .swiper-box .feedback-swiper .feedback-navigation .feedback-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#feedback .swiper-box .feedback-swiper .feedback-navigation .swiper-button-disabled {
  filter: grayscale(1);
}