#presents {
  padding: 90px 0;
  background-color: #f3f3f3;
}
@media (max-width: 767px) {
  #presents {
    padding: 30px 0;
  }
}
#presents .row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  column-gap: 32px;
  width: 100%;
}
@media (max-width: 899px) {
  #presents .row {
    align-items: center;
  }
}
@media (max-width: 767px) {
  #presents .row {
    flex-direction: column;
    align-items: center;
    row-gap: 30px;
  }
}
#presents .title-col {
  max-width: 335px;
}
@media (max-width: 1399px) {
  #presents .title-col {
    position: sticky;
    top: 150px;
  }
}
@media (max-width: 899px) {
  #presents .title-col {
    position: static;
    max-width: auto;
  }
}
@media (max-width: 767px) {
  #presents .title-col {
    max-width: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 575px) {
  #presents .title-col {
    max-width: 260px;
  }
}
#presents .title-col__title {
  margin-bottom: 18px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 50px;
  line-height: 61px;
  color: #4f4f4f;
}
#presents .title-col__title span {
  color: #f82c2c;
}
@media (max-width: 767px) {
  #presents .title-col__title {
    text-align: center;
  }
}
@media (max-width: 575px) {
  #presents .title-col__title {
    max-width: 250px;
    margin-bottom: 13px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.01em;
  }
}
#presents .title-col__text {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
@media (max-width: 767px) {
  #presents .title-col__text {
    text-align: center;
  }
}
@media (max-width: 575px) {
  #presents .title-col__text {
    font-size: 16px;
    line-height: 22px;
  }
}
#presents .title-col__btn {
  max-width: 260px;
  min-height: 64px;
  margin-top: 29px;
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;
}
@media (max-width: 767px) {
  #presents .title-col__btn {
    text-align: center;
    width: 260px;
  }
}
@media (max-width: 575px) {
  #presents .title-col__btn {
    border-radius: 10px;
    margin-top: 19px;
    font-size: 18px;
    line-height: 22px;
  }
}
#presents .presents-col {
  padding-top: 10px;
  max-width: 1050px;
  width: 100%;
}
@media (max-width: 899px) {
  #presents .presents-col {
    max-width: 330px;
  }
}
@media (max-width: 767px) {
  #presents .presents-col {
    padding-top: 0;
  }
}
@media (max-width: 575px) {
  #presents .presents-col {
    max-width: 260px;
  }
}
#presents .presents-col .presents-swiper {
  position: relative;
  width: 100%;
  overflow: hidden;
}
#presents .presents-col .swiper-wrapper {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
  row-gap: 32px;
  column-gap: 30px;
}
@media (max-width: 899px) {
  #presents .presents-col .swiper-wrapper {
    flex-wrap: nowrap;
    column-gap: 0;
    row-gap: 0;
  }
}
#presents .presents-col .present {
  display: flex;
  align-items: stretch;
  min-height: 240px;
  width: calc(25% - 22.5px);
  font-family: "Inter", sans-serif;
}
@media (max-width: 1399px) {
  #presents .presents-col .present {
    width: calc(33.3333333333% - 20px);
  }
}
@media (max-width: 1199px) {
  #presents .presents-col .present {
    width: calc(50% - 15px);
  }
}
#presents .presents-col .present__wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  height: auto;
  width: 100%;
  padding: 18px 20px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
}
#presents .presents-col .present__link {
  position: absolute;
  right: 13px;
  top: 14px;
  cursor: pointer;
}
#presents .presents-col .present__link svg circle {
  stroke: #4f4f4f;
  transition: 0.3s all;
}
#presents .presents-col .present__link svg path {
  fill: #4f4f4f;
  transition: 0.3s all;
}
#presents .presents-col .present__link:hover svg circle {
  stroke: #f82c2c;
}
#presents .presents-col .present__link:hover svg path {
  fill: #f82c2c;
}
#presents .presents-col .present__img-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 135px;
  margin-bottom: 1px;
}
#presents .presents-col .present__img-wrap img {
  object-fit: contain;
  max-width: 100%;
}
#presents .presents-col .present__name {
  margin-bottom: 1px;
  font-family: inherit;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}
#presents .presents-col .present__price {
  font-family: inherit;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #828282;
}
#presents .presents-col .present__price span {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: line-through;
}
#presents .presents-col .present-end .present__wrap {
  justify-content: center;
  background: rgb(255, 255, 255);
}
#presents .presents-col .present-end .present__name {
  margin-bottom: 6px;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
}
#presents .presents-col .present-end .present__price {
  font-family: inherit;
  font-size: 20px;
  line-height: 24px;
  color: #f82c2c;
}
#presents .presents-col .present-end .present__price span {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: line-through;
}
#presents .presents-col .presents-pagination {
  display: flex;
  justify-content: center;
  margin-top: 16px;
  width: 100%;
  z-index: 2;
}
@media (min-width: 900px) {
  #presents .presents-col .presents-pagination {
    display: none;
  }
}
#presents .presents-col .presents-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
}
#presents .presents-col .presents-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#presents .presents-col .presents-navigation {
  position: absolute;
  top: calc(50% - 18px);
  transform: translateY(-50%);
  z-index: 2;
  width: 100%;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 900px) {
  #presents .presents-col .presents-navigation {
    display: none;
  }
}
#presents .presents-col .presents-navigation .presents-btn {
  position: relative;
  width: 28px;
  height: 28px;
  background-position: center center;
  background-size: 28px 28px;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#presents .presents-col .presents-navigation .presents-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#presents .presents-col .presents-navigation .presents-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#presents .presents-col .presents-navigation .swiper-button-disabled {
  filter: grayscale(1);
}