#steps {
  padding: 153px 0 157px;
  border-top: 1px solid #bdbdbd;
  background: rgba(255, 255, 255, 0.78);
}
@media (max-width: 991px) {
  #steps {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  #steps {
    padding: 70px 0 70px;
  }
}
@media (max-width: 575px) {
  #steps {
    padding: 25px 0 15px;
    border-top: none;
  }
}
@media (max-width: 575px) {
  #steps .content-container {
    padding: 0;
  }
}
#steps .title-box {
  margin-bottom: 68px;
}
@media (max-width: 575px) {
  #steps .title-box {
    padding: 0 20px;
    margin-bottom: 24px;
  }
}
#steps .row {
  display: flex;
  justify-content: center;
  align-items: stretch;
  margin: 0 -15px;
  row-gap: 30px;
}
@media (max-width: 1199px) {
  #steps .row {
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  #steps .row {
    margin: 0;
  }
}
@media (max-width: 575px) {
  #steps .row {
    row-gap: 0;
  }
}
#steps .card {
  width: 33.3333333333%;
  padding: 0 15px;
  font-family: "Inter", sans-serif;
  color: #828282;
  background: #ffffff;
  transition: 0.7s all;
}
@media (max-width: 1199px) {
  #steps .card {
    width: 50%;
  }
}
@media (max-width: 991px) {
  #steps .card {
    width: 80%;
  }
}
@media (max-width: 767px) {
  #steps .card {
    width: 100%;
    padding: 0;
  }
}
#steps .card__wrap {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
  width: 100%;
  border: 1px solid #828282;
  border-radius: 10px;
  overflow: hidden;
}
@media (max-width: 575px) {
  #steps .card__wrap {
    border: none;
    border-radius: 0;
  }
}
#steps .card__img-wrap {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  background: #f3f3f3;
}
#steps .card__img-wrap img {
  max-width: 100%;
}
@media (min-width: 576px) {
  #steps .card__img-wrap img {
    aspect-ratio: 509/258;
  }
}
#steps .card__text-wrap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-grow: 1;
  width: 100%;
  padding: 20px 20px 20px 30px;
}
@media (max-width: 575px) {
  #steps .card__text-wrap {
    padding: 20px 20px 25px 30px;
  }
}
#steps .card__number-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 7px;
  min-width: 69px;
  margin: -69px auto 17px 0;
  aspect-ratio: 1/1;
  padding: 5px;
  border-radius: 5px;
  color: #ffffff;
}
@media (max-width: 575px) {
  #steps .card__number-box {
    margin-bottom: 12px;
  }
}
#steps .card__number {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  text-align: center;
}
#steps .card__number-text {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
#steps .card__link {
  position: relative;
  pointer-events: none;
}
@media (max-width: 575px) {
  #steps .card__link {
    pointer-events: all;
    padding-right: 40px;
  }
}
#steps .card__link svg {
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
}
@media (min-width: 576px) {
  #steps .card__link svg {
    display: none;
  }
}
#steps .card__link svg path {
  stroke: #828282;
}
#steps .card__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
}
@media (max-width: 575px) {
  #steps .card__title {
    margin-bottom: 3px;
  }
}
#steps .card__subtitle {
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #828282;
}
#steps .card__text {
  margin-top: 8px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #828282;
}
@media (max-width: 575px) {
  #steps .card__text {
    display: none;
  }
}
#steps .card__lists-box {
  display: flex;
  align-items: flex-start;
  column-gap: 8.25%;
  margin-top: auto;
  padding-top: 8px;
}
@media (max-width: 575px) {
  #steps .card__lists-box {
    display: none;
  }
}
#steps .card__lists-item {
  width: 1px;
  flex-grow: 1;
}
#steps .card__lists-item:first-child {
  flex-grow: 1.1;
}
#steps .card__lists-title {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #828282;
}
#steps .card__lists-list {
  list-style: disc;
  padding-left: 16px;
  color: #828282;
}
#steps .card__lists-list li {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #828282;
}
#steps .card-1 .card__number-box {
  background-color: #333333;
}
#steps .card-1 .card__title {
  color: #333333;
}
#steps .card-1 .card__link svg path {
  stroke: #333333;
}
#steps .card-2 .card__number-box {
  background-color: #007aeb;
}
#steps .card-2 .card__title {
  color: #007aeb;
}
#steps .card-2 .card__link svg path {
  stroke: #007aeb;
}
#steps .card-3 .card__number-box {
  background-color: #f82c2c;
}
#steps .card-3 .card__title {
  color: #f82c2c;
}
#steps .card-3 .card__link svg path {
  stroke: #f82c2c;
}

#step-modal {
  display: none;
  padding: 28px 24px 36px;
  font-family: "Inter", sans-serif;
  border-radius: 3px;
}
#step-modal .step-modal__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
}
#step-modal .step-modal__title_1 {
  color: #333333;
}
#step-modal .step-modal__title_2 {
  color: #007aeb;
}
#step-modal .step-modal__title_3 {
  color: #f82c2c;
}
#step-modal .step-modal__subtitle {
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #828282;
}
#step-modal .step-modal__text {
  margin-top: 8px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #828282;
}
#step-modal .step-modal__lists-box {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding-top: 17px;
}
#step-modal .step-modal__lists-item {
  width: 100%;
}
#step-modal .step-modal__lists-item:not(:first-child) {
  margin-top: 17px;
}
#step-modal .step-modal__lists-title {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #828282;
}
#step-modal .step-modal__lists-list {
  list-style: disc;
  padding-left: 16px;
  color: #828282;
}
#step-modal .step-modal__lists-list li {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #828282;
}