#map {
  padding: 84px 0 0;
  font-family: "Inter", sans-serif;
}
#map .title-box {
  margin-bottom: 45px;
}
@media (max-width: 575px) {
  #map .title-box {
    margin-bottom: 20px;
  }
}
#map .title-box__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #007aeb;
}
@media (max-width: 575px) {
  #map .title-box__title {
    font-size: 24px;
    line-height: 29px;
    text-align: left;
  }
}
#map .title-box__subtitle {
  margin-top: 5px;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.6;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #map .title-box__subtitle {
    margin-top: 10px;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.6;
    text-align: left;
  }
}
#map .list-box {
  display: flex;
  justify-content: space-between;
  column-gap: 16px;
  max-width: 975px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 767px) {
  #map .list-box {
    flex-direction: column;
    row-gap: 20px;
  }
}
#map .list-box__list {
  display: flex;
  flex-direction: column;
  row-gap: 9px;
  width: 100%;
}
@media (max-width: 767px) {
  #map .list-box__list {
    row-gap: 20px;
  }
}
#map .list-box__list li {
  position: relative;
  padding-left: 22px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.4;
  color: #4f4f4f;
}
#map .list-box__list li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #007aeb;
}
#map .list-box__list_1 {
  max-width: 436px;
}
@media (max-width: 767px) {
  #map .list-box__list_1 {
    max-width: 100%;
  }
}
#map .list-box__list_2 {
  max-width: 392px;
}
@media (max-width: 767px) {
  #map .list-box__list_2 {
    max-width: 100%;
  }
}
#map .content-container_map {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0;
  margin-top: 77px;
  height: 745px;
  max-height: 70vh;
  overflow: hidden;
}
@media (max-width: 767px) {
  #map .content-container_map {
    margin-top: 25px;
  }
}
#map .content-container_map .map-item {
  height: 745px;
  max-height: 70vh;
}
#map .content-container_map .ymaps-2-1-79-ground-pane {
  filter: grayscale(100%);
}
#map .page-loader-wrap {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f3f3;
  transform: scale(1.5);
  opacity: 0.8;
  z-index: 10000;
}
#map .page-loader-wrap .lds-spinner {
  color: official;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
#map .page-loader-wrap .lds-spinner div {
  transform-origin: 40px 40px;
  animation: lds-spinner 1.2s linear infinite;
}
#map .page-loader-wrap .lds-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #1871dd;
}
#map .page-loader-wrap .lds-spinner div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
#map .page-loader-wrap .lds-spinner div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}