#callme.callme-new {
  display: none;
  width: 100%;
  max-width: 504px;
  padding: 45px 18px 19px 18px !important;
  background: #ffffff;
  border-radius: 3px;
  font-family: "Montserrat", sans-serif;
}
#callme.callme-new .callme-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 390px;
  margin: 0 auto;
}
#callme.callme-new .callme__title {
  margin: 0 0 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  text-transform: none;
  color: #000000;
}
#callme.callme-new .callme__text {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #323232;
}
@media (max-width: 575px) {
  #callme.callme-new .callme__text {
    font-size: 12px;
    line-height: 15px;
  }
}
#callme.callme-new .form-container {
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-top: 29px;
}
@media (max-width: 575px) {
  #callme.callme-new .form-container {
    margin-top: 3px;
  }
}
#callme.callme-new .call-modal {
  flex-direction: column;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 100%;
}
#callme.callme-new .callme__inputs-box {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  row-gap: 24px;
  width: 100%;
  margin-bottom: 24px;
}
#callme.callme-new .callme__inputs-box .callme__label {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
#callme.callme-new .callme__inputs-box .callme__label span {
  margin-bottom: 9px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
}
#callme.callme-new .callme__inputs-box .callme__label input {
  height: auto;
  padding: 18px 25px;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
#callme.callme-new .callme__form-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 263px;
  min-height: 63px;
  padding: 10px 15px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: none;
}
#callme.callme-new .callme__info {
  max-width: 280px;
  margin: 15px auto 0;
  font-family: inherit;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #2f2f2f;
  opacity: 0.9;
}
@media (max-width: 575px) {
  #callme.callme-new .callme__info {
    margin: 13px auto 0;
  }
}
#callme.callme-new .callme__info a {
  color: #007aeb;
  transition: 0.3s all;
}
#callme.callme-new .callme__info a:hover {
  color: #f82c2c;
}