.inputs-box {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  row-gap: 27px;
  margin-bottom: 18px;
}
@media (max-width: 575px) {
  .inputs-box {
    row-gap: 20px;
    margin-bottom: 28px;
  }
}
.inputs-box .inputs-box__label {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.inputs-box .inputs-box__label span {
  margin-bottom: 11px;
  font-family: inherit;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  .inputs-box .inputs-box__label span {
    margin-bottom: 6px;
    font-size: 16px;
    line-height: 22px;
  }
}
.inputs-box .inputs-box__label input {
  padding: 23px 24px;
  font-family: inherit;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 575px) {
  .inputs-box .inputs-box__label input {
    width: 100%;
    padding: 15px 24px;
    font-size: 16px;
    line-height: 22px;
  }
}