#delivery {
  padding: 150px 0 115px;
  background: #ffffff;
}
@media (max-width: 991px) {
  #delivery {
    padding: 80px 0 80px;
  }
}
@media (max-width: 767px) {
  #delivery {
    padding: 60px 0 60px;
  }
}
@media (max-width: 575px) {
  #delivery {
    padding: 26px 0 46px;
  }
}
#delivery .title-box {
  margin-bottom: 40px;
}
@media (max-width: 575px) {
  #delivery .title-box {
    margin-bottom: 30px;
  }
}
#delivery .row {
  display: flex;
  column-gap: 40px;
  width: 100%;
  font-family: "Inter", sans-serif;
}
@media (max-width: 767px) {
  #delivery .row {
    flex-direction: column;
    align-items: center;
  }
}
#delivery .form-container {
  width: 100%;
  max-width: 100%;
  padding: 0;
}
@media (max-width: 1399px) {
  #delivery .form-container {
    position: sticky;
    top: 130px;
  }
}
@media (max-width: 767px) {
  #delivery .form-container {
    position: static;
  }
}
#delivery .delivery__title {
  width: 100%;
  margin-bottom: 17px;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #323232;
}
@media (max-width: 767px) {
  #delivery .delivery__title {
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  #delivery .delivery__title {
    font-size: 16px;
    line-height: 20px;
  }
}
#delivery .delivery__form-wrap {
  flex-direction: column;
  display: flex;
  align-items: center;
  flex-basis: 510px;
  max-width: 32%;
}
@media (max-width: 1399px) {
  #delivery .delivery__form-wrap {
    max-width: 42%;
  }
}
@media (max-width: 991px) {
  #delivery .delivery__form-wrap {
    max-width: 46%;
  }
}
@media (max-width: 767px) {
  #delivery .delivery__form-wrap {
    flex-basis: auto;
    max-width: 100%;
    width: 100%;
  }
}
#delivery .delivery__form {
  flex-direction: column;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 100%;
}
#delivery .delivery__inputs-box {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  row-gap: 24px;
  width: 100%;
  margin-bottom: 0;
}
@media (max-width: 575px) {
  #delivery .delivery__inputs-box {
    row-gap: 20px;
  }
}
#delivery .delivery__inputs-box .delivery__label {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
#delivery .delivery__inputs-box .delivery__label span {
  margin-bottom: 11px;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}
@media (max-width: 575px) {
  #delivery .delivery__inputs-box .delivery__label span {
    margin-bottom: 9px;
    font-size: 16px;
    line-height: 22px;
  }
}
#delivery .delivery__inputs-box .delivery__label input {
  height: 71px;
  padding: 18px 25px;
  margin: 0;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 575px) {
  #delivery .delivery__inputs-box .delivery__label input {
    font-size: 16px;
    line-height: 22px;
  }
}
#delivery .delivery__form-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 282px;
  min-height: 77px;
  margin-top: 40px;
  padding: 10px 15px;
  border-radius: 20px;
  font-family: inherit;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: none;
}
@media (max-width: 575px) {
  #delivery .delivery__form-btn {
    min-height: 62px;
    margin-top: 27px;
    border-radius: 10px;
  }
}
#delivery .delivery__features-wrap {
  flex-grow: 1;
  padding-top: 33px;
}
@media (max-width: 767px) {
  #delivery .delivery__features-wrap {
    padding-top: 42px;
  }
}
#delivery .delivery__features {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  column-gap: 20px;
  row-gap: 40px;
  width: 100%;
  padding: 55px 0;
  margin-bottom: 46px;
  border-top: 1px solid #828282;
  border-bottom: 1px solid #828282;
}
@media (max-width: 1399px) {
  #delivery .delivery__features {
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  #delivery .delivery__features {
    padding: 0;
    margin-bottom: 27px;
    border: none;
  }
}
#delivery .delivery__feature {
  display: flex;
  align-items: center;
  column-gap: 4px;
}
@media (max-width: 1399px) {
  #delivery .delivery__feature {
    width: 100%;
    max-width: 320px;
  }
}
@media (max-width: 575px) {
  #delivery .delivery__feature:nth-child(1) .delivery__feature-icon {
    padding-left: 4px;
  }
  #delivery .delivery__feature:nth-child(2) .delivery__feature-icon {
    padding-right: 8px;
  }
  #delivery .delivery__feature:nth-child(3) .delivery__feature-icon {
    padding-right: 8px;
    padding-left: 12px;
  }
}
#delivery .delivery__feature-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 75px;
}
#delivery .delivery__feature-icon img {
  max-width: 100%;
}
@media (max-width: 575px) {
  #delivery .delivery__feature-icon {
    min-width: 57px;
    max-width: 57px;
  }
}
#delivery .delivery__feature-title {
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #007aeb;
}
@media (max-width: 575px) {
  #delivery .delivery__feature-title {
    font-size: 18px;
    line-height: 22px;
  }
}
#delivery .delivery__feature-text {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #828282;
}
@media (max-width: 575px) {
  #delivery .delivery__feature-text {
    font-size: 16px;
    line-height: 22px;
  }
}
#delivery .delivery__features-imgs {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  column-gap: 40px;
  row-gap: 27px;
  flex-wrap: wrap;
  width: 100%;
}
@media (max-width: 767px) {
  #delivery .delivery__features-imgs {
    column-gap: 54px;
  }
}