#zmeevik {
  padding: 148px 0 160px;
  background: #ffffff;
}
@media (max-width: 991px) {
  #zmeevik {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  #zmeevik {
    padding: 70px 0 70px;
  }
}
@media (max-width: 575px) {
  #zmeevik {
    padding: 58px 0 5px;
  }
}
#zmeevik .title-box {
  margin-bottom: 43px;
}
@media (max-width: 575px) {
  #zmeevik .title-box {
    margin-bottom: 22px;
  }
}
#zmeevik .row {
  display: flex;
  align-items: flex-start;
  width: 100%;
}
@media (max-width: 767px) {
  #zmeevik .row {
    flex-direction: column-reverse;
    align-items: center;
  }
}
#zmeevik .zmeevik__features {
  position: relative;
  overflow: hidden;
  font-family: "Inter", sans-serif;
}
@media (max-width: 1399px) {
  #zmeevik .zmeevik__features {
    width: 60%;
  }
}
@media (max-width: 1199px) {
  #zmeevik .zmeevik__features {
    width: 50%;
  }
}
@media (max-width: 767px) {
  #zmeevik .zmeevik__features {
    width: 100%;
    max-width: 490px;
  }
}
#zmeevik .zmeevik__features-wrapper {
  width: 100%;
}
@media (min-width: 767px) {
  #zmeevik .zmeevik__features-wrapper {
    display: grid;
    grid-template-columns: 1fr 1.035fr;
    grid-template-rows: auto;
    column-gap: 32px;
    row-gap: 56px;
  }
}
@media (max-width: 1199px) {
  #zmeevik .zmeevik__features-wrapper {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 991px) {
  #zmeevik .zmeevik__features-wrapper {
    row-gap: 32px;
  }
}
#zmeevik .zmeevik__features-item {
  display: flex;
  align-items: flex-start;
  width: 100%;
}
#zmeevik .zmeevik__features-item_special {
  padding: 26px;
  background: #f3f3f3;
  border-radius: 10px;
  max-width: 471px;
}
@media (max-width: 991px) {
  #zmeevik .zmeevik__features-item_special {
    padding: 16px;
  }
}
@media (max-width: 767px) {
  #zmeevik .zmeevik__features-item_special {
    max-width: 490px;
  }
}
@media (max-width: 575px) {
  #zmeevik .zmeevik__features-item_special {
    align-items: center;
    padding: 16px 16px 20px 25px;
  }
}
@media (min-width: 1200px) {
  #zmeevik .zmeevik__features-item_special + .zmeevik__features-item {
    height: max-content;
    align-self: center;
  }
}
#zmeevik .zmeevik__features-img-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  max-width: 105px;
  margin-right: 22px;
  padding-top: 3px;
}
@media (max-width: 575px) {
  #zmeevik .zmeevik__features-img-wrap {
    min-width: 85px;
    margin-right: 15px;
    justify-content: flex-start;
  }
}
#zmeevik .zmeevik__features-text-wrap {
  flex-grow: 1;
}
#zmeevik .zmeevik__features-title {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #007aeb;
}
#zmeevik .zmeevik__features-text {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #zmeevik .zmeevik__features-text {
    font-size: 16px;
    line-height: 22px;
  }
}
#zmeevik .zmeevik__features .zmeevik-pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  padding-bottom: 4px;
  padding-left: 30px;
  padding-right: 30px;
  width: 100%;
  z-index: 2;
}
@media (min-width: 767px) {
  #zmeevik .zmeevik__features .zmeevik-pagination {
    display: none;
  }
}
#zmeevik .zmeevik__features .zmeevik-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
}
#zmeevik .zmeevik__features .zmeevik-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#zmeevik .zmeevik__features .zmeevik-navigation {
  position: absolute;
  bottom: 0;
  z-index: 2;
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 767px) {
  #zmeevik .zmeevik__features .zmeevik-navigation {
    display: none;
  }
}
#zmeevik .zmeevik__features .zmeevik-navigation .zmeevik-btn {
  position: relative;
  width: 28px;
  height: 28px;
  background-position: center;
  background-size: 28px 28px;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#zmeevik .zmeevik__features .zmeevik-navigation .zmeevik-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#zmeevik .zmeevik__features .zmeevik-navigation .zmeevik-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#zmeevik .zmeevik__features .zmeevik-navigation .swiper-button-disabled {
  filter: grayscale(1);
}
@media (max-width: 1399px) {
  #zmeevik .zmeevik__img-wrap {
    position: sticky;
    top: 150px;
    width: 40%;
  }
  #zmeevik .zmeevik__img-wrap img {
    width: 100%;
  }
}
@media (max-width: 1199px) {
  #zmeevik .zmeevik__img-wrap {
    width: 50%;
  }
}
@media (max-width: 767px) {
  #zmeevik .zmeevik__img-wrap {
    position: static;
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 30px;
  }
  #zmeevik .zmeevik__img-wrap img {
    width: auto;
    max-width: 100%;
  }
}