#features {
  padding: 149px 0 0;
  border-top: 1px solid #bdbdbd;
  overflow: hidden;
}
@media (max-width: 991px) {
  #features {
    padding: 100px 0 0;
  }
}
@media (max-width: 767px) {
  #features {
    padding: 80px 0 0;
  }
}
@media (max-width: 575px) {
  #features {
    padding: 30px 0 0;
  }
}
#features .title-box {
  margin-bottom: 60px;
}
@media (max-width: 575px) {
  #features .title-box {
    margin-bottom: 27px;
  }
}
#features .features {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
}
#features .features .feature {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  width: 100%;
}
@media (max-width: 1399px) {
  #features .features .feature {
    flex-direction: column;
  }
}
#features .features .feature:nth-child(2n) {
  flex-direction: row-reverse;
}
@media (max-width: 1399px) {
  #features .features .feature:nth-child(2n) {
    flex-direction: column;
  }
}
#features .features .feature__img-wrap {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 50%;
  flex-shrink: 0;
}
#features .features .feature__img-wrap:not(.feature__img-wrap_slider) {
  background-color: #e7e7e7;
}
@media (max-width: 1399px) {
  #features .features .feature__img-wrap {
    width: 100%;
  }
}
#features .features .feature__img-wrap img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (max-width: 1399px) {
  #features .features .feature__img-wrap img {
    max-height: 600px;
  }
}
#features .features .feature__img {
  width: 100%;
}
#features .features .feature__slider {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
@media (max-width: 575px) {
  #features .features .feature__slider {
    overflow: visible;
    margin-bottom: 50px;
  }
}
#features .features .feature__slider .swiper-slide {
  height: auto;
}
#features .features .feature__slider .swiper-slide picture {
  height: 100%;
}
#features .features .feature__slider .swiper-slide img {
  object-fit: cover;
}
#features .features .feature__slider .feature-pagination {
  position: absolute;
  bottom: 34px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  margin-top: 116px;
  width: 100%;
  z-index: 2;
}
@media (max-width: 575px) {
  #features .features .feature__slider .feature-pagination {
    bottom: -50px;
    justify-content: flex-start;
    width: 100%;
    padding: 0 20px;
    margin-top: 19px;
  }
}
#features .features .feature__slider .feature-pagination span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  margin: 0 19px;
  background: rgba(255, 255, 255, 0.7);
  border: 2px solid #007aeb;
  color: #007aeb;
  opacity: 1;
  transition: 0.3s all;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
}
@media (min-width: 768px) {
  #features .features .feature__slider .feature-pagination span:hover {
    background-color: #007aeb;
    color: #ffffff;
  }
}
@media (max-width: 575px) {
  #features .features .feature__slider .feature-pagination span {
    width: 31px;
    height: 31px;
    margin: 0 18px 0 0;
  }
}
#features .features .feature__slider .feature-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
  color: #ffffff;
}
#features .features .feature__text-wrap {
  width: 100%;
  max-width: 910px;
  padding: 35px 50px;
  font-family: "Inter", sans-serif;
}
@media (max-width: 1399px) {
  #features .features .feature__text-wrap {
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  #features .features .feature__text-wrap {
    padding: 35px 20px;
  }
}
@media (max-width: 575px) {
  #features .features .feature__text-wrap {
    padding: 9px 20px 35px;
  }
}
#features .features .feature__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #007aeb;
}
@media (max-width: 575px) {
  #features .features .feature__title {
    font-size: 24px;
    line-height: 29px;
  }
}
#features .features .feature__subtitle {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #features .features .feature__subtitle {
    display: none;
  }
}
#features .features .feature__text {
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #features .features .feature__text {
    font-size: 16px;
    line-height: 22px;
  }
  #features .features .feature__text br {
    display: none;
  }
}
#features .features .feature__list-title {
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #features .features .feature__list-title {
    font-size: 16px;
    line-height: 22px;
  }
}
#features .features .feature__list {
  display: flex;
  flex-direction: column;
  padding-top: 5px;
  row-gap: 10px;
  width: 100%;
}
#features .features .feature__list-item {
  display: flex;
  align-items: flex-start;
  column-gap: 10px;
}
#features .features .feature__list-number {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #007aeb;
}
@media (max-width: 575px) {
  #features .features .feature__list-number {
    font-size: 16px;
    line-height: 22px;
  }
}
#features .features .feature__list-text {
  font-weight: 300;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #features .features .feature__list-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
  }
}
#features .features .feature__list-text span {
  font-weight: 700;
}
#features .features .feature__btn-box {
  display: flex;
  align-items: center;
  column-gap: 42px;
  margin-top: 40px;
}
@media (max-width: 767px) {
  #features .features .feature__btn-box {
    flex-direction: column-reverse;
    row-gap: 18px;
    margin-top: 20px;
  }
}
#features .features .feature__btn {
  min-height: 77px;
  max-width: 278px;
  width: 100%;
  padding: 10px;
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 0.01em;
  color: #ffffff;
}
@media (max-width: 575px) {
  #features .features .feature__btn {
    min-height: 55px;
    max-width: 268px;
    border-radius: 10px;
    font-size: 18px;
    line-height: 22px;
  }
}
#features .features .feature__price {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #f82c2c;
}
@media (max-width: 575px) {
  #features .features .feature__price {
    font-size: 28px;
    line-height: 34px;
  }
}