#experiment {
  padding: 151px 0 158px;
  background: #ffffff;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 991px) {
  #experiment {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
  #experiment {
    padding: 70px 0 70px;
  }
}
@media (max-width: 575px) {
  #experiment {
    padding: 35px 0 25px;
  }
}
#experiment .title-box {
  margin-bottom: 32px;
}
@media (max-width: 575px) {
  #experiment .title-box {
    margin-bottom: 0;
  }
}
#experiment .row {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 42px;
}
@media (max-width: 991px) {
  #experiment .row {
    column-gap: 32px;
  }
}
@media (max-width: 767px) {
  #experiment .row {
    flex-wrap: wrap;
    row-gap: 32px;
  }
}
@media (max-width: 575px) {
  #experiment .row {
    row-gap: 0;
  }
}
@media (max-width: 1299px) {
  #experiment .img-wrap {
    width: calc(50% - 21px);
  }
}
@media (max-width: 767px) {
  #experiment .img-wrap {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
@media (max-width: 575px) {
  #experiment .img-wrap {
    max-width: 100%;
    padding: 0 5px;
  }
}
#experiment .img-wrap img {
  max-width: 100%;
}
@media (max-width: 767px) {
  #experiment .img-wrap img {
    max-width: 400px;
  }
}
@media (max-width: 575px) {
  #experiment .img-wrap img {
    max-width: 100%;
  }
}
#experiment .text-wrap {
  max-width: 640px;
}
@media (max-width: 1299px) {
  #experiment .text-wrap {
    width: calc(50% - 21px);
  }
}
@media (max-width: 767px) {
  #experiment .text-wrap {
    width: 100%;
  }
}
#experiment .text-wrap__subtitle {
  margin-bottom: 22px;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #experiment .text-wrap__subtitle {
    margin-bottom: 13px;
    font-size: 20px;
    line-height: 24px;
  }
}
#experiment .text-wrap__subtitle span {
  color: #f82c2c;
}
#experiment .text-wrap__btn {
  width: 135px;
  min-height: 43px;
  border-radius: 10px;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.01em;
}
@media (min-width: 576px) {
  #experiment .text-wrap__btn {
    display: none;
  }
}

#arom-modal {
  font-family: "Inter", sans-serif;
}
@media (max-width: 575px) {
  #arom-modal {
    display: none;
    padding: 42px 20px 30px;
    border-radius: 3px;
  }
}
@media (min-width: 576px) {
  #arom-modal {
    display: block !important;
  }
}
#arom-modal .arom-modal__text {
  margin-bottom: 45px;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 991px) {
  #arom-modal .arom-modal__text {
    margin-bottom: 32px;
  }
}
@media (max-width: 575px) {
  #arom-modal .arom-modal__text {
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 22px;
  }
}
#arom-modal .arom-modal__price-wrap {
  display: flex;
  align-items: baseline;
  column-gap: 15px;
  margin-bottom: 12px;
}
#arom-modal .arom-modal__price-old {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: line-through;
  color: #4f4f4f;
}
#arom-modal .arom-modal__price {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #4f4f4f;
}
#arom-modal .arom-modal__btn {
  min-height: 77px;
  width: 267px;
  padding: 10px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
}
@media (max-width: 575px) {
  #arom-modal .arom-modal__btn {
    min-height: 62px;
    letter-spacing: 0.01em;
  }
}