#models {
  padding: 180px 0 46px;
  background: #f3f3f3;
}
@media (max-width: 991px) {
  #models {
    padding: 100px 0 46px;
  }
}
@media (max-width: 767px) {
  #models {
    padding: 80px 0 46px;
  }
}
@media (max-width: 575px) {
  #models {
    padding: 26px 0 15px;
  }
}
@media (max-width: 575px) {
  #models .content-container {
    padding: 0;
  }
}
#models .title-box {
  margin-bottom: 24px;
}
@media (max-width: 575px) {
  #models .title-box {
    margin-bottom: 28px;
    padding: 0 20;
  }
}
#models .cards {
  position: relative;
  margin: 0 -15px;
  padding: 16px 0 12px;
  overflow: hidden;
}
@media (min-width: 768px) {
  #models .cards {
    margin: 0 -15px;
  }
}
@media (max-width: 1280px) and (min-width: 768px) {
  #models .cards {
    padding: 0 0 12px;
  }
}
@media (max-width: 767px) {
  #models .cards {
    width: 100%;
    max-width: 500px;
  }
}
@media (max-width: 575px) {
  #models .cards {
    padding-bottom: 0;
  }
}
#models .cards .cards-pagination {
  position: static;
  display: flex;
  justify-content: center;
  column-gap: 5px;
  width: 100%;
  margin-bottom: -4px;
  z-index: 2;
}
@media (min-width: 768px) {
  #models .cards .cards-pagination {
    display: none;
  }
}
#models .cards .cards-pagination div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  min-height: 54px;
  margin: 0;
  border-radius: 5px 5px 0px 0px;
  opacity: 1;
  color: #828282;
  background: rgba(255, 255, 255, 0.5);
  transition: 0.3s all;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
}
#models .cards .cards-pagination div.swiper-pagination-bullet-active {
  color: #f82c2c;
  background: rgb(255, 255, 255);
}
#models .cards .cards-pagination div:last-child {
  color: #f82c2c;
  background: rgba(248, 44, 44, 0.35);
}
#models .cards .cards-pagination div:last-child.swiper-pagination-bullet-active {
  color: #ffffff;
  background: rgb(248, 44, 44);
}
@media (min-width: 768px) {
  #models .cards .swiper-wrapper {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    row-gap: 40px;
    width: 100%;
  }
}
#models .cards .card {
  display: flex;
  flex-direction: column;
  height: auto;
  font-family: "Inter", sans-serif;
}
@media (min-width: 767px) {
  #models .cards .card {
    width: 25%;
    padding: 0 15px;
  }
}
@media (max-width: 1280px) and (min-width: 768px) {
  #models .cards .card {
    width: 50%;
  }
}
#models .cards .card__special-title {
  visibility: hidden;
  margin-bottom: 17px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  color: transparent;
}
@media (max-width: 767px) {
  #models .cards .card__special-title {
    display: none;
  }
}
#models .cards .card__wrap {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 22px;
  border-radius: 10px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  #models .cards .card__wrap {
    border-radius: 0;
  }
}
#models .cards .card__img-wrap {
  width: 100%;
  flex-shrink: 0;
  cursor: zoom-in;
}
#models .cards .card__img-wrap img {
  width: 100%;
  object-fit: contain;
}
#models .cards .card__text-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  margin-top: 15px;
}
@media (max-width: 575px) {
  #models .cards .card__text-wrap {
    margin-top: 18px;
  }
}
#models .cards .card__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #models .cards .card__text {
    font-size: 16px;
    line-height: 22px;
  }
}
#models .cards .card__list {
  width: 100%;
  margin-top: 14px;
  margin-bottom: 14px;
}
@media (max-width: 575px) {
  #models .cards .card__list {
    margin-bottom: 4px;
  }
}
#models .cards .card__list-item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  column-gap: 6px;
  width: 100%;
  padding: 8px 0;
}
#models .cards .card__list-item:not(:first-child) {
  border-top: 1px solid rgba(0, 0, 0, 0.25);
}
#models .cards .card__list-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: -0.02em;
  color: #4f4f4f;
}
#models .cards .card__list-number {
  white-space: nowrap;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #4f4f4f;
}
#models .cards .card__old-price {
  margin-top: auto;
  margin-bottom: 6px;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  text-decoration-line: line-through;
  color: #007aeb;
}
#models .cards .card__price {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #007aeb;
}
#models .cards .card__btn {
  max-width: 326px;
  width: 100%;
  min-height: 77px;
  margin-top: 13px;
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 0.01em;
}
@media (max-width: 575px) {
  #models .cards .card__btn {
    min-height: 58px;
    border-radius: 10px;
  }
}
#models .cards .card__special {
  position: relative;
}
#models .cards .card__special::before {
  content: "";
  position: absolute;
  left: 3px;
  top: -16px;
  width: calc(100% - 6px);
  height: calc(100% + 28px);
  background: #f82c2c;
  opacity: 0.25;
  border-radius: 10px;
}
@media (max-width: 767px) {
  #models .cards .card__special::before {
    display: none;
  }
}
#models .cards .card__special .card__special-title {
  position: relative;
  visibility: visible;
  color: #f82c2c;
}
#models .cards .card__special .card__wrap {
  position: relative;
}
@media (max-width: 767px) {
  #models .cards .card__special .card__wrap {
    outline: 4px solid #f82c2c;
    outline-offset: -4px;
  }
}
#models .cards .card__special .card__old-price {
  color: #f82c2c;
}
#models .cards .card__special .card__price {
  color: #f82c2c;
}