
@import "../../scss/variables";

#guarantee {
  padding: 137px 0 132px;
  background-color: $section-gray-bg;

  @media (max-width: 767px) {
    padding: 80px 0 60px;
    background-color: #ffffff;
  }

  @media (max-width: 575px) {
    padding: 22px 0 40px;
  }

  .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    row-gap: 34px;
    column-gap: 39px;
    width: 100%;

    @media (max-width: 575px) {
      row-gap: 11px;
    }
  }

  .title-box {
    width: 100%;

    &__title {
      @media (max-width: 767px) and (min-width: 576px) {
        font-weight: 700;
        font-size: 40px;
        line-height: 43px;
      }

      span {
        color: $primary-color;
      }
    }
  }

  .guarantee__content {
    display: flex;
    justify-content: center;
    align-items: center;
    row-gap: 34px;
    column-gap: 50px;
    width: 100%;

    @media (max-width: 991px) {
      column-gap: 40px;
    }

    @media (max-width: 767px) {
      flex-wrap: wrap;
      column-gap: 16px;
      row-gap: 24px;
    }

    @media (max-width: 575px) {
      row-gap: 11px;
    }
  }

  .imgs-wrap {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
    width: 100%;
    max-width: 510px;

    @media (max-width: 575px) {
      gap: 20px;
      margin-top: 10px;
      margin-bottom: 10px;
      max-width: 100%;
    }

    .img-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      width: calc(50% - 15px);

      @media (max-width: 575px) {
        width: calc(50% - 10px);
      }
    }

    picture {
      width: 100%;
    }

    img {
      width: 100%;
      aspect-ratio: 1/1;
    }
  }

  .text-wrap {
    width: 100%;
    max-width: 510px;
    font-family: "Inter", sans-serif;

    .text-wrap__text {
      margin-bottom: 28px;
      font-family: "Montserrat", sans-serif;
      font-weight: 700;
      font-size: 30px;
      line-height: 120%;
      color: $primary-color;

      @media (max-width: 767px) {
        text-align: center;
      }

      @media (max-width: 575px) {
        margin-bottom: 35px;
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
      }
    }

    .text-wrap__list {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      row-gap: 20px;
      padding-left: 35px;

      li {
        position: relative;
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        color: #2f2f2f;

        @media (max-width: 575px) {
          font-size: 16px;
          line-height: 22px;
        }

        &::before {
          content: "";
          position: absolute;
          top: 2px;
          left: -35px;
          width: 24px;
          height: 24px;
          background-image: url("../images/global/check-circle.svg");
          background-size: 100% 100%;
        }
      }
    }
  }
}
