#new-header {
  padding-top: 24px;
  padding-bottom: 40px;
  background: #f3f3f3;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 575px) {
  #new-header {
    padding-top: 21px;
    padding-bottom: 0;
  }
}
#new-header * {
  box-sizing: border-box;
}
#new-header .page-loader-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f3f3;
  transform: scale(1.5);
  z-index: 10000;
}
#new-header .page-loader-wrap .lds-spinner {
  color: official;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
#new-header .page-loader-wrap .lds-spinner div {
  transform-origin: 40px 40px;
  animation: lds-spinner 1.2s linear infinite;
}
#new-header .page-loader-wrap .lds-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #1871dd;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
#new-header .page-loader-wrap .lds-spinner div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
#new-header .content-container {
  overflow: visible;
}
#new-header .grid-box {
  display: grid;
  grid-template-columns: auto auto 375px;
  grid-template-rows: min-content auto 1fr;
  width: 100%;
  row-gap: 50px;
  column-gap: 16px;
}
@media (max-width: 1619px) {
  #new-header .grid-box {
    row-gap: 40px;
    grid-template-columns: auto auto;
    max-width: 1200px;
    margin: 0 auto;
  }
}
@media (max-width: 1199px) {
