
@import "../../scss/variables";
#kit {
  padding: 150px 0 75px;
  background: #ffffff;
  border-bottom: 1px solid #bdbdbd;
  border-top: 1px solid #bdbdbd;

  @media (max-width: 991px) {
    padding: 75px 0 75px;
  }

  @media (max-width: 767px) {
    padding: 60px 0 60px;
  }

  @media (max-width: 575px) {
    border: none;
    padding: 30px 0 0;
  }

  img:not([src]):not([srcset]) {
    visibility: hidden;
  }

  .title-box {
    margin-bottom: 3px;

    @media (max-width: 575px) {
      margin-bottom: 10px;
    }
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.4s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
    opacity: 0 !important;
  }

  .kit {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    font-family: "Inter", sans-serif;

    @media (max-width: 1399px) {
      flex-wrap: wrap;
    }

    &__dop-wrap {
      max-width: 545px;
      width: 100%;
      padding-top: 85px;

      @media (max-width: 1199px) {
        padding-top: 55px;
        max-width: 48%;
      }

      @media (max-width: 991px) {
        max-width: 52%;
      }

      @media (max-width: 767px) {
        max-width: 100%;
      }

      @media (max-width: 575px) {
        padding-top: 0;
      }

      .dops-item {
        display: flex;
        align-items: center;
        column-gap: 15px;
        padding: 12px 0;
        width: 100%;

        &:not(:first-child) {
          padding-top: 27px;
          border-top: 1px solid rgba(0, 0, 0, 0.2);
        }

        @media (max-width: 575px) {
          column-gap: 0;
          padding: 4px 0 7px;
        }

        &__icon {
          max-width: 68px;

          img {
            max-width: 100%;
          }
        }

        &__text-wrap {
          display: flex;
          justify-content: space-between;
          align-items: center;
          column-gap: 15px;
          flex-grow: 1;

          @media (max-width: 575px) {
            flex-grow: 0;
            flex-direction: column;
            align-items: stretch;
            max-width: 170px;
            width: 100%;
            margin-left: auto;
          }
        }

        &__name {
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          letter-spacing: 0.01em;
          text-decoration-line: underline;
          color: $text-dark;
          transition: 0.3s all;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 19px;
          }

          &:hover {
            color: #ff0000;
          }
        }

        &__price-wrap {
          margin-left: auto;

          @media (max-width: 575px) {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            column-gap: 10px;
            margin-left: 0;
          }
        }

        &__price {
          font-weight: 700;
          font-size: 20px;
          line-height: 28px;
          text-align: right;
          color: $text-dark;
          white-space: nowrap;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 28px;
          }
        }

        &__oldprice {
          font-weight: 400;
          font-size: 16px;
          line-height: 19px;
          text-align: right;
          text-decoration-line: line-through;
          color: $text-dark;
          white-space: nowrap;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 19px;
          }
        }
      }
    }

    &__img-wrap {
      max-width: 570px;
      width: 100%;
      aspect-ratio: 57/84;
      margin-left: auto;

      @media (max-width: 1399px) {
        position: sticky;
        top: 110px;
        max-width: 520px;
        margin-right: auto;
      }

      @media (max-width: 1199px) {
        max-width: 44%;
      }

      @media (max-width: 991px) {
        max-width: 47%;
        margin-right: 0;
      }

      @media (max-width: 767px) {
        position: static;
        max-width: 450px;
        margin-right: auto;
      }

      .img-box {
        position: relative;
        width: 100%;
        height: 100%;

        @media (max-width: 1199px) and (min-width: 992px) {
          max-height: 85vh;
        }

        &__main {
          position: relative;
          display: grid;
          grid-template-columns: 1fr;
          height: 100%;
        }

        &__main-product {
          position: absolute;
          left: 11.579%;
          top: 0;
          grid-row: 1/2;
          grid-column: 1/2;
          width: 59.122%;
          height: 81.428%;

          @media (max-width: 1199px) and (min-width: 992px) {
            display: flex;
            justify-content: center;
          }

          img {
            width: 100%;
          }
        }

        .img-dop {
          position: absolute;
          opacity: 0.4;
          transition: 0.3s all;

          &_show {
            opacity: 1;
          }

          img {
            position: static;
            max-width: 100%;
            height: auto;
          }

          &.arom-dop {
            width: 27%;
            bottom: 2.111%;
            right: 25.47%;
          }

          &.dioptr-dop {
            width: 22.375%;
            bottom: 10%;
            right: 21.36%;
          }

          &.kolonna-dop {
            width: 20.683%;
            right: 21.4188%;
            bottom: 28.666%;
          }

          &.popugai-dop {
            width: 28.376%;
            right: -0.6666%;
            bottom: 2.7777%;
          }

          &.spn-m-dop {
            width: 39.83%;
            left: 3.157%;
            bottom: 7.666%;
          }

          &.spn-n-dop {
            width: 46.8376%;
            bottom: 0.6666%;
            left: 9.12%;
          }

          &.zarga-dop {
            width: 19.145%;
            bottom: 21.8888%;
            left: 0;
          }
        }
      }
    }

    &__form-wrap {
      max-width: 400px;
      width: 100%;
      padding-top: 64px;

      @media (max-width: 1399px) {
        padding-top: 0;
        max-width: 545px;
        margin-top: 20px;
      }

      @media (max-width: 1199px) {
        max-width: 48%;
      }

      @media (max-width: 991px) {
        max-width: 52%;
      }

      @media (max-width: 767px) {
        max-width: 100%;
      }

      .form-box {
        width: 100%;
        padding: 24px 28px 34px;
        background: $section-gray-bg;
        border-radius: 10px;

        @media (max-width: 575px) {
          width: calc(100% + 40px);
          padding: 24px 20px 24px;
          margin: 0 -20px;
          border-radius: 0;
        }

        .inputs-box {
          row-gap: 20px;

          @media (max-width: 575px) {
            row-gap: 15px;
          }
        }

        &__radio-btns {
          display: flex;
          flex-direction: column;
          row-gap: 8px;
          width: 100%;
          margin-bottom: 14px;
        }

        &__radio-label {
          position: relative;
          display: flex;
          align-items: center;
          column-gap: 20px;
          padding: 6px 0 10px;
          cursor: pointer;

          &:not(:last-child) {
            border-bottom: 1px solid #e0e0e0;
          }

          div {
            position: relative;
            width: 32px;
            height: 32px;
            border: 2px solid $text-dark;
            border-radius: 50%;
            background: transparent;
            transition: 0.3s all;
          }

          &:hover div {
            border-color: #ff0000;
            box-shadow: 0 0 4px 1px rgba(255, 0, 0, 0.3);
          }

          input {
            position: absolute;
            visibility: hidden;
            width: 1px;
            height: 0;
            opacity: 0;
            z-index: -1;

            &:checked + div {
              &:before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 13px;
                height: 13px;
                border-radius: 50%;
                background: $btnred;
              }
            }
          }
        }

        &__radio-name {
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          color: $text-black-gray;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 22px;
          }
        }

        &__radio-price {
          margin-left: auto;
          font-weight: 700;
          font-size: 20px;
          line-height: 28px;
          color: $text-black-gray;
          text-align: right;

          @media (max-width: 575px) {
            font-size: 16px;
            line-height: 22px;
          }

          &_old {
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            text-align: right;
            text-decoration-line: line-through;
          }
        }

        &__price-info-wrap {
          display: flex;
          justify-content: space-between;
          align-items: center;
          column-gap: 20px;
          margin: 14px 0 20px;
        }

        &__price-text {
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          text-transform: uppercase;
          color: $text-black-gray;

          @media (max-width: 575px) {
            font-size: 18px;
            line-height: 22px;
          }
        }

        &__price-number {
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          text-align: right;
          color: $btnred;

          @media (max-width: 575px) {
            font-size: 18px;
            line-height: 22px;
          }
        }

        &__form-btn {
          width: 100%;
          min-height: 77px;
          margin-top: 32px;
          border-radius: 20px;
          font-family: "Montserrat", sans-serif;
          font-weight: 700;
          font-size: 22px;
          line-height: 27px;
          text-align: center;
          letter-spacing: 0.01em;
          text-transform: uppercase;

          @media (max-width: 575px) {
            min-height: 62px;
            margin-top: 27px;
            border-radius: 10px;
          }
        }

        &__rassrochka-check {
          display: flex;
          align-items: center;
          justify-content: center;
          column-gap: 10px;
          margin-top: 24px;

          div {
            width: 24px;
            height: 24px;
            background-size: 16px 16px;
          }

          span {
            font-family: "Inter", sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            display: flex;
            align-items: center;
            letter-spacing: 0.01em;
            color: $text-black-gray;

            @media (max-width: 575px) {
              font-size: 20px;
              line-height: 24px;
            }
          }

          &:hover {
            div {
              border-color: $btnred;
              box-shadow: 0 0 4px 1px rgb(192 33 33 / 32%);
            }
          }
        }

        &__info {
          margin-top: 20px;
          opacity: 0.9;
          font-family: "Inter", sans-serif;
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 15px;
          text-align: center;
          color: $text-black-gray;
          a {
            color: $primary-color;
            transition: 0.3s all;

            &:hover {
              color: #ff0000;
            }
          }
        }
      }
    }
  }
}
