#mechanics {
  padding: 150px 0 48px;
  background: #ffffff;
}
@media (max-width: 991px) {
  #mechanics {
    padding: 100px 0 48px;
  }
}
@media (max-width: 575px) {
  #mechanics {
    padding: 26px 0 27px;
  }
}
#mechanics .title-box {
  width: 100%;
  margin-bottom: 85px;
}
@media (max-width: 991px) {
  #mechanics .title-box {
    margin-bottom: 45px;
  }
}
@media (max-width: 767px) {
  #mechanics .title-box {
    margin-bottom: 24px;
  }
}
#mechanics .row {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 40px;
  width: 100%;
}
@media (max-width: 1199px) {
  #mechanics .row {
    gap: 20px;
  }
}
@media (max-width: 991px) {
  #mechanics .row {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}
#mechanics .mechanics {
  max-width: 648px;
  width: 100%;
}
@media (max-width: 1199px) {
  #mechanics .mechanics {
    width: calc(50% - 10px);
  }
}
@media (max-width: 991px) {
  #mechanics .mechanics {
    max-width: 100%;
    width: 100%;
  }
}
@media (max-width: 767px) {
  #mechanics .mechanics {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 0;
    margin-top: 15px;
  }
}
#mechanics .mechanics__img-wrap {
  position: relative;
  max-width: 700px;
  width: 100%;
}
@media (max-width: 1199px) {
  #mechanics .mechanics__img-wrap {
    max-width: calc(50% - 10px);
  }
}
@media (max-width: 991px) {
  #mechanics .mechanics__img-wrap {
    max-width: 700px;
  }
}
@media (max-width: 767px) {
  #mechanics .mechanics__img-wrap {
    max-width: 500px;
  }
}
@media (max-width: 575px) {
  #mechanics .mechanics__img-wrap {
    max-width: 100%;
    padding: 0 5.973%;
  }
}
#mechanics .mechanics__img-wrap video {
  max-width: 100%;
}
@media (min-width: 768px) {
  #mechanics .mechanics__wrap {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }
}
@media (min-width: 768px) {
  #mechanics .mechanics__item {
    height: auto;
  }
}
#mechanics .mechanics__text {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
#mechanics .mechanics__text span {
  font-weight: 700;
  color: #007aeb;
}
#mechanics .mechanics .mechanics-pagination {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  padding-top: 4px;
  padding-left: 30px;
  padding-right: 30px;
  width: 100%;
  z-index: 2;
}
@media (min-width: 768px) {
  #mechanics .mechanics .mechanics-pagination {
    display: none;
  }
}
#mechanics .mechanics .mechanics-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
}
#mechanics .mechanics .mechanics-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#mechanics .mechanics .mechanics-navigation {
  position: absolute;
  top: 0;
  z-index: 2;
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 768px) {
  #mechanics .mechanics .mechanics-navigation {
    display: none;
  }
}
#mechanics .mechanics .mechanics-navigation .mechanics-btn {
  position: relative;
  width: 28px;
  height: 28px;
  background-color: #ffffff;
  background-position: center;
  background-size: 28px 28px;
  background-repeat: no-repeat;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#mechanics .mechanics .mechanics-navigation .mechanics-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#mechanics .mechanics .mechanics-navigation .mechanics-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#mechanics .mechanics .mechanics-navigation .swiper-button-disabled {
  filter: grayscale(1);
}