#consultation {
  padding: 49px 0 37px;
  background: #ffffff;
}
@media (max-width: 767px) {
  #consultation {
    padding: 27px 0 27px;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
  }
}
#consultation .grid-box {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  row-gap: 21px;
  column-gap: 30px;
  justify-content: center;
  width: 100%;
}
@media (max-width: 991px) {
  #consultation .grid-box {
    grid-template-columns: 1fr;
    column-gap: 22px;
  }
}
#consultation .grid-box__title {
  grid-column: 2/3;
  grid-row: 1/2;
  align-self: end;
}
@media (max-width: 991px) {
  #consultation .grid-box__title {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
#consultation .grid-box__img {
  grid-column: 1/2;
  grid-row: 1/3;
}
@media (max-width: 991px) {
  #consultation .grid-box__img {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
#consultation .grid-box__text {
  grid-column: 2/3;
  grid-row: 2/3;
  align-self: flex-start;
}
@media (max-width: 991px) {
  #consultation .grid-box__text {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}
#consultation .consultation {
  font-family: "Inter", sans-serif;
}
#consultation .consultation__title {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #4f4f4f;
}
@media (max-width: 991px) {
  #consultation .consultation__title {
    max-width: 500px;
    margin: 0 auto 7px;
    text-align: center;
  }
}
@media (max-width: 575px) {
  #consultation .consultation__title {
    max-width: 205px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}
@media (max-width: 991px) {
  #consultation .consultation__img-wrap {
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 575px) {
  #consultation .consultation__img-wrap {
    padding-right: 20px;
  }
  #consultation .consultation__img-wrap img {
    max-width: 195px;
  }
}
#consultation .consultation__text {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #4f4f4f;
}
@media (max-width: 991px) {
  #consultation .consultation__text {
    text-align: center;
  }
}
#consultation .consultation__phone-wrap {
  display: flex;
  align-items: flex-start;
  column-gap: 26px;
}
@media (max-width: 991px) {
  #consultation .consultation__phone-wrap {
    flex-direction: column;
    align-items: center;
    margin-bottom: 16px;
  }
}
#consultation .consultation__phone-number {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 38px;
  line-height: 46px;
  color: #f82c2c;
  transition: 0.3s all;
}
@media (max-width: 991px) {
  #consultation .consultation__phone-number {
    text-align: center;
  }
}
@media (max-width: 575px) {
  #consultation .consultation__phone-number {
    font-size: 32px;
    line-height: 39px;
  }
}
#consultation .consultation__phone-number:hover {
  color: rgb(192, 33, 33);
}
#consultation .consultation__phone-text {
  max-width: 180px;
  padding-top: 4px;
}
@media (max-width: 991px) {
  #consultation .consultation__phone-text {
    max-width: 100%;
    padding-top: 6px;
  }
}
@media (max-width: 575px) {
  #consultation .consultation__phone-text {
    font-size: 16px;
    line-height: 19px;
  }
}
#consultation .consultation__btn-wrap {
  display: flex;
  align-items: flex-start;
  column-gap: 32px;
  margin-top: 23px;
}
@media (max-width: 991px) {
  #consultation .consultation__btn-wrap {
    flex-direction: column;
    align-items: center;
  }
}
#consultation .consultation__btn {
  min-height: 77px;
  min-width: 296px;
  padding: 10px 15px;
  border-radius: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
}
#consultation .consultation__btn::before {
  display: none;
}
@media (max-width: 575px) {
  #consultation .consultation__btn {
    min-height: 58px;
    min-width: 262px;
    border-radius: 10px;
  }
}
#consultation .consultation__btn-text {
  max-width: 435px;
  padding-top: 10px;
}
@media (max-width: 991px) {
  #consultation .consultation__btn-text {
    padding-top: 22px;
  }
}
@media (max-width: 575px) {
  #consultation .consultation__btn-text {
    font-size: 20px;
    line-height: 24px;
  }
}