#order-success.order-success-new {
  display: none;
  width: 100%;
  max-width: 586px;
  padding: 53px 18px 50px 18px !important;
  margin-bottom: 0;
  background: #ffffff;
  border-radius: 3px;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 575px) {
  #order-success.order-success-new {
    padding: 45px 18px 19px 18px !important;
  }
}
#order-success.order-success-new .order-success__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 390px;
  margin: 0 auto;
}
#order-success.order-success-new .order-success__title {
  margin: 0 0 12px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #000000;
}
#order-success.order-success-new .order-success__text {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #323232;
}
@media (max-width: 575px) {
  #order-success.order-success-new .order-success__text {
    font-size: 12px;
    line-height: 15px;
  }
}
#order-success.order-success-new form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 17px;
}
#order-success.order-success-new .order-success__label {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
}
#order-success.order-success-new .order-success__label span {
  margin-bottom: 9px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
}
#order-success.order-success-new .order-success__label input {
  height: auto;
  padding: 18px 25px;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
#order-success.order-success-new .order-success__form-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 163px;
  min-height: 63px;
  padding: 10px 15px;
  margin-top: 24px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: none;
}