
@import "../../scss/variables";

#presents {
  padding: 90px 0;
  background-color: $section-gray-bg;

  @media (max-width: 767px) {
    padding: 30px 0;
  }

  .row {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    column-gap: 32px;
    width: 100%;

    @media (max-width: 899px) {
      align-items: center;
    }

    @media (max-width: 767px) {
      flex-direction: column;
      align-items: center;
      row-gap: 30px;
    }
  }

  .title-col {
    max-width: 335px;

    @media (max-width: 1399px) {
      position: sticky;
      top: 150px;
    }

    @media (max-width: 899px) {
      position: static;
      max-width: auto;
    }

    @media (max-width: 767px) {
      max-width: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @media (max-width: 575px) {
      max-width: 260px;
    }

    &__title {
      margin-bottom: 18px;
      font-family: "Montserrat", sans-serif;
      font-weight: 600;
      font-size: 50px;
      line-height: 61px;
      color: $text-black-gray;
      span {
        color: $btnred;
      }

      @media (max-width: 767px) {
        text-align: center;
      }

      @media (max-width: 575px) {
        max-width: 250px;
        margin-bottom: 13px;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 0.01em;
      }
    }

    &__text {
      font-family: "Inter", sans-serif;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: $text-black-gray;

      @media (max-width: 767px) {
        text-align: center;
      }

      @media (max-width: 575px) {
        font-size: 16px;
        line-height: 22px;
      }
    }

    &__btn {
      max-width: 260px;
      min-height: 64px;
      margin-top: 29px;
      border-radius: 20px;
      font-family: "Montserrat", sans-serif;
      font-weight: 700;
      font-size: 22px;
      line-height: 27px;
      display: flex;
      align-items: center;
      letter-spacing: 0.01em;

      @media (max-width: 767px) {
        text-align: center;
        width: 260px;
      }

      @media (max-width: 575px) {
        border-radius: 10px;
        margin-top: 19px;
        font-size: 18px;
        line-height: 22px;
      }
    }
  }

  .presents-col {
    padding-top: 10px;
    max-width: 1050px;
    width: 100%;

    @media (max-width: 899px) {
      max-width: 330px;
    }

    @media (max-width: 767px) {
      padding-top: 0;
    }

    @media (max-width: 575px) {
      max-width: 260px;
    }

    .presents-swiper {
      position: relative;
      width: 100%;
      overflow: hidden;
    }

    .swiper-wrapper {
      display: flex;
      align-items: stretch;
      justify-content: flex-start;
      flex-wrap: wrap;
      row-gap: 32px;
      column-gap: 30px;

      @media (max-width: 899px) {
        flex-wrap: nowrap;
        column-gap: 0;
        row-gap: 0;
      }
    }

    .present {
      display: flex;
      align-items: stretch;
      min-height: 240px;
      width: calc(25% - 90px / 4);
      font-family: "Inter", sans-serif;

      @media (max-width: 1399px) {
        width: calc(100% / 3 - 60px / 3);
      }

      @media (max-width: 1199px) {
        width: calc(100% / 2 - 30px / 2);
      }

      &__wrap {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        height: auto;
        width: 100%;
        padding: 18px 20px;
        background: rgba(255, 255, 255, 0.5);
        border-radius: 10px;
      }

      &__link {
        position: absolute;
        right: 13px;
        top: 14px;
        cursor: pointer;

        svg {
          circle {
            stroke: #4f4f4f;
            transition: 0.3s all;
          }

          path {
            fill: #4f4f4f;
            transition: 0.3s all;
          }
        }

        &:hover {
          svg {
            circle {
              stroke: $btnred;
            }
            path {
              fill: $btnred;
            }
          }
        }
      }

      &__img-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 135px;
        margin-bottom: 1px;
        img {
          object-fit: contain;
          max-width: 100%;
        }
      }

      &__name {
        margin-bottom: 1px;
        font-family: inherit;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        letter-spacing: 0.01em;
        color: $text-black-gray;
      }

      &__price {
        font-family: inherit;
        text-align: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #828282;

        span {
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          text-decoration-line: line-through;
        }
      }
    }

    .present-end {
      .present__wrap {
        justify-content: center;
        background: rgba(255, 255, 255, 1);
      }

      .present__name {
        margin-bottom: 6px;
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
      }

      .present__price {
        font-family: inherit;
        font-size: 20px;
        line-height: 24px;
        color: $btnred;

        span {
          font-weight: 500;
          font-size: 20px;
          line-height: 24px;
          text-decoration-line: line-through;
        }
      }
    }

    .presents-pagination {
      display: flex;
      justify-content: center;
      margin-top: 16px;
      width: 100%;
      z-index: 2;

      @media (min-width: 900px) {
        display: none;
      }

      span {
        width: 20px;
        height: 20px;
        margin: 0 6px;
        background-color: #bdbdbd;
        opacity: 1;

        &.swiper-pagination-bullet-active {
          background-color: $primary-color;
        }
      }
    }

    .presents-navigation {
      position: absolute;
      top: calc(50% - 18px);
      transform: translateY(-50%);
      z-index: 2;
      width: 100%;
      padding: 0 10px;
      display: flex;
      justify-content: space-between;
      pointer-events: none;

      @media (min-width: 900px) {
        display: none;
      }

      .presents-btn {
        position: relative;
        width: 28px;
        height: 28px;
        background-position: center center;
        background-size: 28px 28px;
        background-repeat: no-repeat;
        cursor: pointer;
        pointer-events: all;
        transition: 0.3s all;
      }

      .presents-btn_next {
        background-image: url("../images/global/slider-next.svg");
      }
      .presents-btn_prev {
        background-image: url("../images/global/slider-prev.svg");
      }

      .swiper-button-disabled {
        filter: grayscale(1);
      }
    }
  }
}
