#working-mode {
  padding: 152px 0 46px;
  overflow: hidden;
}
@media (max-width: 991px) {
  #working-mode {
    padding: 100px 0 46px;
  }
}
@media (max-width: 767px) {
  #working-mode {
    padding: 41px 0 25px;
  }
}
#working-mode .title-box {
  width: 100%;
  margin-bottom: 45px;
}
@media (max-width: 575px) {
  #working-mode .title-box {
    margin-bottom: 21px;
  }
}
@media (max-width: 575px) {
  #working-mode .title-box__subtitle {
    margin-top: 8px;
  }
}
#working-mode .mods {
  font-family: "Inter", sans-serif;
  width: 100%;
}
#working-mode .mods .mods__togglers {
  position: relative;
  width: 100%;
}
#working-mode .mods .mods__togglers::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 101vw;
  height: 1px;
  background-color: #333333;
}
#working-mode .mods .mods__togglers .swiper {
  width: 100%;
}
#working-mode .mods .mods__togglers .swiper-wrapper {
  justify-content: space-between;
}
#working-mode .mods .mods__togglers .swiper-slide {
  height: auto;
  width: 222px !important;
  margin-right: 6px;
  cursor: pointer;
  border: 1px #e0e0e0 solid;
  border-bottom: 1px transparent solid;
  border-radius: 10px 10px 0px 0px;
  transition: 0.3s border-color;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__togglers .swiper-slide {
    width: 160px !important;
  }
}
#working-mode .mods .mods__togglers .swiper-slide .swiper-slide-content {
  height: 100%;
  padding: 20px 12px 12px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #828282;
  transition: 0.3s all;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__togglers .swiper-slide .swiper-slide-content {
    padding: 15px 8px 8px;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
  }
}
#working-mode .mods .mods__togglers .swiper-slide:hover {
  border: 1px #333333 solid;
  border-bottom: 1px transparent solid;
}
#working-mode .mods .mods__togglers .swiper-slide_active {
  border: 1px #333333 solid;
}
#working-mode .mods .mods__togglers .swiper-slide_active .swiper-slide-content {
  position: relative;
  color: #333333;
  font-weight: 700;
}
#working-mode .mods .mods__togglers .swiper-slide_active .swiper-slide-content::before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 4px;
  background: #ffffff;
}
#working-mode .mods .fade-enter-active,
#working-mode .mods .fade-leave-active {
  transition: opacity 0.35s ease;
}
#working-mode .mods .fade-enter-from,
#working-mode .mods .fade-leave-to {
  position: absolute;
  width: 100%;
  top: 0;
  opacity: 0;
}
#working-mode .mods .mods__cards {
  position: relative;
  width: 100%;
  margin-top: 86px;
}
@media (max-width: 767px) {
  #working-mode .mods .mods__cards {
    margin-top: 26px;
  }
}
#working-mode .mods .mods__cards .card {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  column-gap: 32px;
  width: 100%;
}
@media (max-width: 767px) {
  #working-mode .mods .mods__cards .card {
    flex-direction: column-reverse;
  }
}
#working-mode .mods .mods__cards .card-text-box {
  max-width: 509px;
  width: 100%;
}
@media (max-width: 767px) {
  #working-mode .mods .mods__cards .card-text-box {
    max-width: 100%;
    margin-top: 20px;
  }
}
#working-mode .mods .mods__cards .card-text-box .card__title {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .card__title {
    display: flex;
    column-gap: 10px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 10px;
  }
}
#working-mode .mods .mods__cards .card-text-box .card__subtitle {
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .card__subtitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }
}
#working-mode .mods .mods__cards .card-text-box .card__text {
  margin-bottom: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .card__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
  }
}
#working-mode .mods .mods__cards .card-text-box .recipe-title {
  margin-top: 12px;
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .recipe-title {
    font-size: 16px;
    line-height: 22px;
  }
}
#working-mode .mods .mods__cards .card-text-box .recipe-list {
  padding: 12px 0 0 16px;
}
#working-mode .mods .mods__cards .card-text-box .recipe-list li {
  position: relative;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .recipe-list li {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
  }
}
#working-mode .mods .mods__cards .card-text-box .recipe-list li:before {
  content: "-";
  position: absolute;
  left: -15px;
  top: 0;
  color: currentColor;
}
#working-mode .mods .mods__cards .card-text-box .features-list {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  row-gap: 34px;
  margin-top: 36px;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .features-list {
    margin-top: 20px;
    row-gap: 16px;
  }
}
#working-mode .mods .mods__cards .card-text-box .features-list li {
  display: flex;
  align-items: flex-start;
  width: 100%;
}
#working-mode .mods .mods__cards .card-text-box .features-list img {
  flex-shrink: 0;
  width: 24px;
  height: auto;
  margin-right: 12px;
  padding-top: 3px;
}
#working-mode .mods .mods__cards .card-text-box .features-list__text-box {
  flex-grow: 1;
}
#working-mode .mods .mods__cards .card-text-box .features-list__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .features-list__title {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
  }
}
#working-mode .mods .mods__cards .card-text-box .features-list__title span {
  font-weight: 700;
  color: #f82c2c;
}
@media (max-width: 575px) {
  #working-mode .mods .mods__cards .card-text-box .features-list__title span {
    font-weight: 400;
  }
}
#working-mode .mods .mods__cards .card-imgs-box {
  max-width: 1050px;
  width: 100%;
}
@media (max-width: 1299px) {
  #working-mode .mods .mods__cards .card-imgs-box {
    max-width: calc(100% - 509px - 32px);
  }
}
@media (max-width: 1099px) {
  #working-mode .mods .mods__cards .card-imgs-box {
    position: relative;
    max-width: 330px;
    margin: 0 auto;
  }
}
#working-mode .mods .mods__cards .card-imgs-box .card-imgs-swiper {
  width: 100%;
  overflow: hidden;
}
#working-mode .mods .mods__cards .card-imgs-box .swiper-wrapper {
  display: flex;
  column-gap: 32px;
  width: 100%;
}
@media (max-width: 1300px) {
  #working-mode .mods .mods__cards .card-imgs-box .swiper-wrapper {
    column-gap: 0;
  }
}
#working-mode .mods .mods__cards .card-imgs-box .swiper-slide {
  display: flex;
  width: 1px;
  flex-grow: 1;
}
#working-mode .mods .mods__cards .card-imgs-box .swiper-slide picture {
  display: flex;
}
#working-mode .mods .mods__cards .card-imgs-box .swiper-slide img {
  width: 100%;
  max-width: 100%;
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-pagination {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
@media (max-width: 1099px) {
  #working-mode .mods .mods__cards .card-imgs-box .imgs-pagination {
    position: absolute;
    bottom: 17px;
    width: 100%;
    z-index: 2;
  }
}
@media (min-width: 1300px) {
  #working-mode .mods .mods__cards .card-imgs-box .imgs-pagination {
    display: none;
  }
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-pagination span {
  width: 20px;
  height: 20px;
  margin: 0 6px;
  background-color: #bdbdbd;
  opacity: 1;
  transition: 0.3s all;
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-pagination span:hover {
  background-color: #007aeb;
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-pagination span.swiper-pagination-bullet-active {
  background-color: #007aeb;
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-navigation {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  width: 100%;
  padding: 0 13px;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
}
@media (min-width: 1100px) {
  #working-mode .mods .mods__cards .card-imgs-box .imgs-navigation {
    display: none;
  }
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-navigation .imgs-btn {
  width: 28px;
  height: 28px;
  background-position: center;
  background-size: 100% 100%;
  cursor: pointer;
  pointer-events: all;
  transition: 0.3s all;
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-navigation .imgs-btn:hover:not(.swiper-button-disabled) {
  transform: scale(1.07);
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-navigation .imgs-btn_next {
  background-image: url("../images/global/slider-next.svg");
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-navigation .imgs-btn_prev {
  background-image: url("../images/global/slider-prev.svg");
}
#working-mode .mods .mods__cards .card-imgs-box .imgs-navigation .swiper-button-disabled {
  filter: grayscale(1);
}