#order-modal {
  display: none;
  max-width: 1050px;
  width: 100%;
  padding: 0;
  background: transparent;
}
@media (max-width: 991px) {
  #order-modal {
    max-width: 500px;
  }
}
#order-modal .fade-enter-active,
#order-modal .fade-leave-active {
  transition: opacity 0.4s ease;
}
#order-modal .fade-enter-from,
#order-modal .fade-leave-to {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0 !important;
}
#order-modal .order-modal__wrap {
  display: flex;
  align-items: stretch;
  width: 100%;
  border: 2px solid #828282;
  border-radius: 10px;
  background: #ffffff;
  overflow: hidden;
}
@media (max-width: 991px) {
  #order-modal .order-modal__wrap {
    flex-direction: column;
  }
}
#order-modal .order-modal__imgs-box {
  width: 50%;
}
@media (max-width: 991px) {
  #order-modal .order-modal__imgs-box {
    width: 100%;
  }
}
#order-modal .order-modal__imgs-bg {
  position: relative;
  display: flex;
  width: 100%;
  padding: 16px;
  background-color: #f2f2f2;
  overflow: hidden;
}
@media (min-width: 992px) {
  #order-modal .order-modal__imgs-bg {
    height: 100%;
  }
}
@media (max-width: 991px) {
  #order-modal .order-modal__imgs-bg {
    aspect-ratio: 495/745;
  }
}
#order-modal .order-modal__img-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
}
@media (max-width: 991px) {
  #order-modal .order-modal__img-item {
    max-height: 85vh;
  }
}
#order-modal .order-modal__img-item picture {
  max-height: 100%;
}
#order-modal .order-modal__img-item img {
  max-width: 380px;
  width: auto;
  object-fit: contain;
  max-height: 100%;
}
@media (max-width: 575px) {
  #order-modal .order-modal__img-item img {
    max-width: 330px;
  }
}
#order-modal .order-modal__form-box {
  width: 50%;
  max-width: 440px;
  padding: 32px;
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 991px) {
  #order-modal .order-modal__form-box {
    width: 100%;
  }
}
@media (max-width: 575px) {
  #order-modal .order-modal__form-box {
    padding: 15px 11px;
  }
}
#order-modal .order-modal__title {
  margin-bottom: 10px;
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  color: #4f4f4f;
}
@media (max-width: 991px) {
  #order-modal .order-modal__title {
    text-align: center;
  }
}
@media (max-width: 575px) {
  #order-modal .order-modal__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}
#order-modal .order-modal__text {
  margin-bottom: 34px;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}
@media (max-width: 991px) {
  #order-modal .order-modal__text {
    text-align: center;
  }
}
@media (max-width: 575px) {
  #order-modal .order-modal__text {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 18px;
  }
}
#order-modal .order-modal__text b {
  font-weight: 700;
  font-size: 27px;
  line-height: 33px;
  white-space: nowrap;
}
@media (max-width: 575px) {
  #order-modal .order-modal__text b {
    font-size: 19px;
    line-height: 22px;
  }
}
#order-modal .order-modal__form {
  margin-top: 26px;
}
#order-modal .order-modal__rassrochka-wrap {
  display: flex;
  justify-content: center;
  width: 100%;
  padding-top: 12px;
}
@media (max-width: 575px) {
  #order-modal .order-modal__rassrochka-wrap {
    padding-top: 0;
  }
}
#order-modal .order-modal__rassrochka-check {
  display: flex;
  align-items: center;
  column-gap: 10px;
}
#order-modal .order-modal__rassrochka-check div {
  width: 24px;
  height: 24px;
  background-size: 16px 16px;
}
#order-modal .order-modal__rassrochka-check span {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;
  color: #4f4f4f;
}
@media (max-width: 575px) {
  #order-modal .order-modal__rassrochka-check span {
    font-size: 14px;
    line-height: 17px;
  }
}
#order-modal .order-modal__rassrochka-check:hover div {
  border-color: #f82c2c;
  box-shadow: 0 0 4px 1px rgba(192, 33, 33, 0.32);
}
#order-modal .order-modal__form-btn {
  width: 100%;
  min-height: 77px;
  margin-top: 36px;
  padding: 10px;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
@media (max-width: 575px) {
  #order-modal .order-modal__form-btn {
    min-height: 62px;
    margin-top: 24px;
    font-size: 18px;
    line-height: 22px;
  }
}
#order-modal .order-modal__info {
  margin-top: 20px;
  opacity: 0.9;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #4f4f4f;
}
#order-modal .order-modal__info a {
  color: #007aeb;
  transition: 0.3s all;
}
#order-modal .order-modal__info a:hover {
  color: #ff0000;
}